import _ from 'underscore';
import { Action } from 'sweetapp-contactflow';

const DELAY_TYPE_TRIGGERS = ['delay-after-action', 'delay-after-sequence', 'after-date'];

class ManualAction extends Action {
  constructor({ sequenceId, actionDescriptor, customDateSystem, trigger }) {
    super({ sequenceId, actionDescriptor, customDateSystem, trigger });
    this.type = 'manual-action';
  }

  async doExecute(args) {
    // In the case of a manual action, when the delay is over it is as if the old task was replaced with a new task
    // with a manual trigger (it is not completed immediately).
    // See Action.execute in sweetapp-contactflow/src/action/index.js
    const trigger = this.triggerDescriptor;
    let result = null;
    if (hasDelayTrigger(trigger)) {
      result = {
        trigger: {
          type: 'manual-trigger',
        },
        completion: {
          isCompleted: false,
        },
      };
    }

    return result;
  }
}

export const hasDelayTrigger = (trigger) => {
  if (_.contains(DELAY_TYPE_TRIGGERS, trigger?.type)) {
    return true;
  }
  if (trigger?.type !== '$or' && trigger?.type !== '$and') {
    return false;
  }
  if (!_.isEmpty(_.intersection(_.pluck(trigger?.conditions, 'type'), DELAY_TYPE_TRIGGERS))) {
    return true;
  }
  return false;
};

export default ManualAction;
