import _ from 'underscore';
import React from 'react';

const Timeline = ({ timeline }) => {
  if (_.isEmpty(timeline)) {
    return (
      <div>
        <i>Aucun historique</i>
      </div>
    )
  }
  return (
    <div>
      {_.map(_.sortBy(timeline, 'date').reverse(), timelineItem => (
        <div 
          key={timelineItem.id}
          style={{
            border: '1px solid black',
            borderRadius: 8,
            marginBottom: 10,
            padding: 10,
          }}
        >
          {timelineItem.date} - {timelineItem.text}
        </div>
      ))}
    </div>
  )
}

export default Timeline;