const DEFAULT_AUTOPILOT_DELAY = 24 * 3600 * 1000;

export const getAutopilotDelayInMs = ({ conditionDescriptor }) => {
  if (!conditionDescriptor) {
    return DEFAULT_AUTOPILOT_DELAY;
  }
  const { delay } = conditionDescriptor;
  if (!delay) {
    return DEFAULT_AUTOPILOT_DELAY;
  }
  const { unit, value } = delay;
  if (unit === 'ms') {
    return value;
  }
  if (unit === 'day') {
    return value * 24 * 3600 * 1000;
  }
  throw Error(`Unit ${unit} not supported`);
};

export const linkedinInvitationAcceptedCondition = () => {
  const accept = ({ context }) => {
    if (!context.profile) {
      throw Error('No profile provided to linkedinInvitationAcceptedCondition.accept');
    }
    const { profile } = context;
    return !!profile?.currentSequenceInfo?.performanceData?.linkedinInvitationAccepted?.isAchieved;
  };

  const acceptDate = ({ context }) => {
    if (!context.profile) {
      throw Error('No profile provided to linkedinInvitationAcceptedCondition.acceptDate');
    }
    const { profile } = context;
    const linkedinInvitationAccepted =
      profile?.currentSequenceInfo?.performanceData?.linkedinInvitationAccepted?.isAchieved;
    const invitationAcceptedEventTimestamp =
      profile?.currentSequenceInfo?.performanceData?.linkedinInvitationAccepted?.timestamp;
    if (!linkedinInvitationAccepted || !invitationAcceptedEventTimestamp) {
      return null;
    }
    return new Date(invitationAcceptedEventTimestamp);
  };

  return { accept, acceptDate };
};

const conditionsFactory = {
  getAdditionalConditions: () => ({
    'manual-trigger': () => ({ accept: () => false, acceptDate: () => null }),
    'linkedin-invitation-accepted': linkedinInvitationAcceptedCondition,
  }),
};

export default conditionsFactory;
