import _ from 'underscore';
import { Interserie, Serie } from '../types'
import { getDayAfterOffset } from '../common';

const getInterseriesFromSeriesAndStartDate = ({
  startDate,
  series
} : {
  startDate: string;
  series: Serie[];
}) : Interserie[] => {
  const sortedIntervals = _.sortBy(_.map(series, ({ startDate, endDate }) => ({
    startDate,
    endDate,
  })), 'startDate');

  const interseries : Interserie[] = []; 

  let lastEnd = getDayAfterOffset(startDate, -1);
  for (const interval of sortedIntervals) {
    if (interval.startDate <= lastEnd) {
      if (interval.endDate > lastEnd) {
        lastEnd = interval.endDate;
      }
    } else {
      const interserieStart = getDayAfterOffset(lastEnd,1);
      const interserieEnd = getDayAfterOffset(interval.startDate, -1);
      if (interserieEnd >= interserieStart) {
        interseries.push({ 
          startDate: interserieStart, 
          endDate: interserieEnd 
        });
      }
      lastEnd = interval.endDate;
    }
  }

  interseries.push({
    startDate: getDayAfterOffset(lastEnd,1),
    endDate: '2100-01-01',
  });

  return interseries;
}

export default getInterseriesFromSeriesAndStartDate;