import { getDayAfterBusinessOffset, getFirstBusinessDayEqualOrAfter, getMondayOfNextWeek } from "../../../common";
import { CandidateState, Serie } from "../../../types";

export const getMinimalAdmissibleDate = ({
  currentDate, 
  candidateState, 
} : {
  currentDate: string;
  candidateState: CandidateState;
}) => {
  if (!candidateState.lastActivationDate) {
    return '2023-01-01';
  }

  // last activation
  let result : String = candidateState.lastActivationDate;

  // no two consecutive days
  if (candidateState.lastTouchpointDate) {
    console.log('plif', candidateState.lastTouchpointDate);
    const minDate = getDayAfterBusinessOffset(candidateState.lastTouchpointDate, 2);
    if (minDate > result) {
      result = minDate;
    }
  }

  // no two in same week
  if (candidateState.lastTouchpointDate) {
    const minDate = getMondayOfNextWeek(candidateState.lastTouchpointDate);
    if (minDate > result) {
      result = minDate;
    }
  }

  return result;
}

export const getMinimalSeeableDateOfSerie = ({ 
  serie 
} : {
  serie: Serie,
}) => {

  let minSerieSeeableDate = '2100-01-01';
  for (const contract of serie.contracts) {
    const minContractSeeableDate = getFirstBusinessDayEqualOrAfter(
      contract.creationDate < contract.startDate ? contract.startDate : contract.creationDate
    );
    if (!minSerieSeeableDate || minContractSeeableDate < minSerieSeeableDate) {
      minSerieSeeableDate = minContractSeeableDate;
    }
  }

  return minSerieSeeableDate;
}