import _ from 'underscore';
import { getRandomString } from '../../common';
import React, { useState } from 'react';

const ProjectEditor = ({ baseProject, onLeaveEditionMode, onSave }) => {
  const [projectState, setProjectState] = useState({
    id: baseProject.id,
    title: baseProject.title,
    type: baseProject.type,
  });
  
  const onSetField = (key, value) => {
    setProjectState(current => ({
      ...current,
      [key]: value,
    }));
  }

  return (
    <div
      style={{
        position: 'relative',
        border: '1px solid lightgrey',
        borderRadius: 8,
        marginBottom: 10,
        padding: 10,
        paddingTop: 0,
      }}
    >
      <h4>{baseProject.id ? 'Modifier le vivier' : 'Créer le vivier'}</h4>
      <div>
        Titre:<br/>
        <input 
          value={projectState.title}
          onChange={({ target }) => onSetField('title', target.value)}
        />
      </div>
      <br/>
      <div>
        Type:<br/>
        <select 
          value={projectState.type}
          onChange={e => onSetField('type', e.target.value)}
        >
          {_.map(
            [ 
              { id: 'high-priority', text: 'Prioritaire' },
              { id: 'low-priority', text: 'Secondaire' },
              { id: 'sleeping', text: 'Désactivé' },
            ],
            ({ id, text }) => (
              <option key={id} value={id}>{text}</option>
            )
          )}
        </select>
      </div>
      <br/>

      {onLeaveEditionMode ? (
        <button
          onClick={onLeaveEditionMode}
          style={{
            cursor: 'pointer', 
            marginRight: 10 
          }}
        >
          Annuler
        </button>
      ) : ''}

      <button
        onClick={() => onSave({ project: projectState })}
        style={{ cursor: 'pointer', marginRight: 40 }}
      >
        {projectState.id ? 'Modifier' : 'Créer'}
      </button>

      {projectState.id  ? (
        <button
          onClick={() => onSave({ project: { ...projectState, toDelete: true }})}
          style={{ 
            position: 'absolute',
            right: 10,
            bottom: 10, 
            cursor: 'pointer', 
            marginRight: 10 
          }}
        >
          Supprimer ce vivier
        </button>
      ) : ''}
    </div>
  );
}

const ProjectsEditor = ({ 
  projects, 
  onCreateProject, 
  onUpdateProject,
  onDeleteProject,
}) => {
  const [editionState, setEditionState] = useState({});

  const onSave = ({ project  }) => {
    if (!project.id) {
      onCreateProject({
        id: getRandomString(12),
        ...project,
      })
    } else {
      if (project.toDelete) {
        onDeleteProject({ id: project.id });
      } else {
        onUpdateProject({
          ...project
        })  
      }
    }
    setEditionState({});
  }

  const onLeaveEditionMode = () => {
    setEditionState({});
  }
  return (
    <div>
      {_.isEmpty(projects) ? (
        <div>
          <i>Pas de viviers renseignés</i>
          <br/><br/>
        </div>
      ) : ''}
      {_.map(projects || [], (project) => (
        <div
          key={project.id}
        > 
          {editionState.baseProject?.id === project.id ? (
            <ProjectEditor
              key={editionState.editionKey}
              baseProject={editionState.baseProject}
              onSave={onSave}
              onLeaveEditionMode={onLeaveEditionMode}
            />
          ) : (
            <div 
              style={{
                position: 'relative',
                border: '1px solid black',
                borderRadius: 8,
                marginBottom: 10,
                padding: 10,
                paddingTop: 0,
              }}
            >
              <h5 style={{ marginTop: 10, marginBottom: 10 }}>{project.title}</h5>
              <span>{
                {
                  'high-priority' : 'Prioritaire',
                  'low-priority' :'Secondaire',
                  'sleeping' : 'Désactivé',
                }[project.type]
              }</span>

              <button
                style={{
                  position: 'absolute',
                  top: 8,
                  right: 10,
                  cursor: 'pointer'
                }} 
                onClick={() => setEditionState({
                  editionKey: getRandomString(12),
                  baseProject: project,
                })}
              >
                Modifier
              </button>
            </div>
          )}
        </div>
      ))}
      {(editionState.editionKey && !editionState.baseProject?.id) ? (
        <ProjectEditor
          key={editionState.editionKey}
          baseProject={editionState.baseProject}
          onSave={onSave}
          onLeaveEditionMode={onLeaveEditionMode}
        />
      ) : ''}
      <div>
        {!(editionState.editionKey && !editionState.baseProject?.id) ? (
          <button 
            style={{ cursor: 'pointer' }}
            onClick={() => setEditionState({
              editionKey: getRandomString(12),
              baseProject: { 
                title: `Vivier ${_.shuffle("0123456789").slice(0,4).join('')}`, 
                type: 'high-priority'
              }
            })}
          >
            Ajouter un vivier
          </button>
        ) : ''}
      </div>
    </div>
  )
}

export default ProjectsEditor;