import _ from 'underscore';
import { getDayAfterOffset, getDiffDatesInDays } from "../../common";

export const getContractSmartEndDate = ({ contract }) => {

  if (!contract) {
    throw Error('no contract in getContractSmartEndDate');
  }

  if (contract.realEndDate) {
    return contract.realEndDate;
  }

  if (contract.expectedEndDate) {
    return contract.expectedEndDate;
  }

  let endDate = contract.endDate;
  for (const addendum of contract.addendums || []) {
    if (addendum.endDate >= endDate) {
      endDate = addendum.endDate; 
    }
  }

  return endDate;
}

export const getContractPeriods = (contract, periodType) => {
  const smartEndDate = getContractSmartEndDate({ contract });
  if (periodType === 'all-unit-periods-splitting-slack') {
    let curEnd =  contract.endDate <= smartEndDate ? contract.endDate : smartEndDate;  
    if (curEnd < contract.startDate) {
      return [];
    }
    const periods = [
      { type: 'initial', startDate: contract.startDate, endDate: curEnd },
    ];
    for (const addendum of contract.addendums || []) {
      let newEnd = addendum.endDate <= smartEndDate ? addendum.endDate : smartEndDate; 
      if (newEnd <= curEnd) {
        continue;
      }
      periods.push({
        type: 'addemdum',
        startDate: getDayAfterOffset(curEnd, 1),
        endDate: newEnd,
      });
      curEnd = newEnd;
    }
    if (smartEndDate > curEnd) {
      periods.push({
        type: 'slack',
        startDate: getDayAfterOffset(curEnd, 1),
        endDate: smartEndDate,
      });
    }
    return periods;
  }
  throw Error('unknown periodType to compute contract periods');
}

export const getContractPeriodLength = (contract, periodType) => {

  const smartEndDate = getContractSmartEndDate({ contract });
  if (periodType === 'initial-period') {
    return getDiffDatesInDays(
      contract.startDate, 
      smartEndDate <= contract.endDate ? smartEndDate : contract.endDate
    );
  }
  if (periodType === 'full-contract') {
    return getDiffDatesInDays(contract.startDate, smartEndDate);
  }
  if (periodType === 'last-meta-period') {
    return _.isEmpty(contract.addendums) ? (
      getDiffDatesInDays(contract.startDate, smartEndDate)
    ) : (
      getDiffDatesInDays(contract.addendums[0].startDate, smartEndDate)
    );
  }
  if (periodType === 'last-unit-period') {
    return _.isEmpty(contract.addendums) ? (
      getDiffDatesInDays(contract.startDate, smartEndDate)
    ) : (
      getDiffDatesInDays(_.last(contract.addendums).startDate, smartEndDate)
    );
  }
  if (periodType === 'last-unit-period-splitting-slack') {
    let maxContractualEndDate = contract.endDate;
    for (const addendum of contract.addendums || []) {
      if (addendum.endDate >= maxContractualEndDate) {
        maxContractualEndDate = addendum.endDate; 
      }
    }
    const slackIsUsed = smartEndDate > maxContractualEndDate;
    return slackIsUsed ? (
      getDiffDatesInDays(maxContractualEndDate, smartEndDate) - 1
    ) : _.isEmpty(contract.addendums) ? (
      getDiffDatesInDays(contract.startDate, smartEndDate)
    ) : getDiffDatesInDays(_.last(contract.addendums).startDate, smartEndDate);
  }

  throw Error('unknown periodType to compute contract period');
}


