import _ from 'underscore';
import { React, useMemo } from 'react';

const NB_MS_IN_A_DAY = 24*3600*1000;

const WeekCalendar = ({ week, currentDate, nextActionDate, oneDayLaterDate, shownEventsByDay, onGoToDay }) => (
  <div style={{ width: '100%', marginTop: 10 }}>
    {_.map(week.days, (day, index) => (
      <div 
        key={day.date}
        style={{
          position: 'relative',
          display: 'inline-block', 
          width: '13.5%', 
          height: '70px', 
          border: nextActionDate === day.date ? '3px solid grey' : '1px solid grey',
          paddingTop: '5px',
          backgroundColor: index < 5 ? (
            currentDate === day.date ? '#f7f3b7' : '#fcfcf7'
          ) : (
            currentDate === day.date ? '#807f7a' : '#e6e6e3'
          ),
          ...day.date >= oneDayLaterDate && {
            cursor: 'pointer'
          },
        }}
        onClick={() => {
          if (day.date >= oneDayLaterDate) {
            onGoToDay(day.date);
          }
        }}
      >
        <center>
        {{
          '0': 'Lun.',
          '1': 'Mar.',
          '2': 'Mer.',
          '3': 'Jeu.',
          '4': 'Ven',
          '5': 'Sam.',
          '6': 'Dim.',
        }[index]}
        {' '}
        {day.date.slice(8)} 
        {' '}
        {
          {
            '01': 'janv.',
            '02': 'févr.',
            '03': 'mars',
            '04': 'avr.',
            '05': 'mai',
            '06': 'juin',
            '07': 'juil.',
            '08': 'août',
            '09': 'sept.',
            '10': 'oct.',
            '11': 'nov.',
            '12': 'déc.',
          }[day.date.slice(5,7)]
        }
        </center>
        <div 
          style={{ 
            position: 'absolute', 
            bottom: 10, 
            left: '50%',
            transform: "translate(-50%, 0%)",
          }}
        >
          {_.map(shownEventsByDay[day.date], (shownEvent) => (
            <div key={shownEvent.id} style={{ display: 'inline-block', padding: 1 }}>
              <span 
                style={{ 
                  color: shownEvent.color, 
                  textDecoration: shownEvent.underlined ? 'underline' : 'none' 
                }} 
                title={shownEvent.id}
              >
                {shownEvent.title}
              </span>
            </div>
          ))}
        </div>
      </div>
    ))}
    
  </div>
);


const Calendar = ({ initialDate, currentDate, shownEvents, nextActionDate, onGoToDay }) => {

  const shownEventsByDay = {};

  for (const shownEvent of shownEvents) {
    if (!shownEventsByDay[shownEvent.date]) {
      shownEventsByDay[shownEvent.date] = [];
    }
    shownEventsByDay[shownEvent.date].push(shownEvent);
  }

  const weeks = useMemo(() => {
    const allWeeks = [];
    const firstMonday = new Date(
      new Date(initialDate).getTime() - ((new Date(initialDate).getDay()-1+7)%7)*NB_MS_IN_A_DAY 
    ).toISOString().slice(0,10)
  
    let day = firstMonday;
    for (let iWeek = 0; iWeek < 48; iWeek++) {
      const week = {
        id: day,
        days: []
      };
      for (let iDay = 0; iDay < 7; iDay++) {
        week.days.push({ date: day });
        day = new Date(new Date(day).getTime()+NB_MS_IN_A_DAY).toISOString().slice(0,10)
      }
      allWeeks.push(week);
    }
    return allWeeks;
  }, []);

  const oneDayLaterDate = new Date(
    new Date(currentDate).getTime() + NB_MS_IN_A_DAY
  ).toISOString().slice(0,10)

  return (
    <div>
      {_.map(weeks, week => (
        <WeekCalendar 
          key={week.id} 
          week={week} 
          currentDate={currentDate} 
          nextActionDate={nextActionDate}
          shownEventsByDay={shownEventsByDay}
          oneDayLaterDate={oneDayLaterDate}
          onGoToDay={onGoToDay}
        />
      ))}
    </div>
  )
}

export default Calendar;