import _ from 'underscore';
import { getRandomString, sleep } from './common.js';
import React, { useState, useEffect } from 'react';
import getContactFlowSimulatorSystem from './contactFlows/index.js';
import baseContactFlowDescriptor from './baseContactFlowDescriptor.ts';
import getSmartlyUpdatedCFDAndEmittedEvents from './animation/index.ts';

import Calendar from './components/Calendar/index.js';
import CurrentSequence from './components/CurrentSequence/index.js';
import ContractsEditor from './components/ContractsEditor/index.js';
import ProjectsEditor from './components/ProjectsEditor/index.js';
import Timeline from './components/Timeline/index.js';
import ActivityEditor from './components/ActivityEditor/index.js';
import AssertionEditor from './components/AssertionEditor/index.js';
import getAssertionValidator from './assertions/index.js';
import TraceManager from './components/TraceManager/index.js';
import { getContractFirstStartEventDate, getContractSecondStartEventDate } from './animation/rules_v1/contractStartEvents.js';
import { getContractFirstEndEventDate, getContractSecondEndEventDate } from './animation/rules_v1/contractEndEvents.js';
import CommentEditor from './components/CommentEditor/index.js';
import { getContractFollowupEvents } from './animation/rules_v1/contractFollowupEvents.js';
import { isContractCollectable } from './animation/rules_v1/collectability.js';
import { getSeriesFromContracts } from './helpers/series.ts';
import { AgencyType, CandidateState, Contract, RollingTask, Serie, SiretCandidateState } from './types.ts';
import { mergeCandidateSiretStates, getCandidateStateAfterReduction } from './animation/rules_v2/candidateState.ts';

const contactFlowSimulatorSystem = getContactFlowSimulatorSystem();

const codeFromEventType = {
  'start-1': 'DM',
  'start-2': 'DM',
  'followup': 'SM',
  'intermission-followup': "SI",
  'end-1': 'FM',
  'end-2': 'FM',
  'payment-point': 'PP',
  'collect': 'RM',
};

const getColorFromIndex = ({ index }) => {
  return [
    '#40E0D0',
    '#DFFF00',
    '#FF7F50',
    '#DE3163', 
    '#9FE2BF',
    '#FFBF00',
    '#6495ED',
    '#00FF00',
    '#FF0000',
    '#0000FF',
  ][index] || 'black';
}


const ExternalSimulator = () => {
  const [executionVersion, setExecutionVersion] = useState('exec-0');
  const [playableTrace, setPlayableTrace] = useState(null);
  const [startDate, setStartDate] = useState(new Date().toISOString().slice(0,10));

  const rerun = async () => {
    setExecutionVersion(null);
    contactFlowSimulatorSystem.setCurrentDate(startDate);
    setPlayableTrace(null);
    await sleep(300);
    setExecutionVersion(getRandomString(12));
  }

  const loadPlayableTrace = async ({ playableTrace }) => {
    if (playableTrace[0]?.type !== 'init-date' || !playableTrace[0]?.date) {
      return alert('first trace item should be of type init-date')
    }
    const initDate = playableTrace[0].date;
    setStartDate(initDate)
    setExecutionVersion(null);
    setPlayableTrace([
      { ...playableTrace[0], isPlayed: true },
      ...playableTrace.slice(1),
    ])
    contactFlowSimulatorSystem.setCurrentDate(initDate);
    await sleep(300);
    setExecutionVersion(getRandomString(12));
  }

  if (!executionVersion) {
    return <div />
  }

  return (
    <div>
      <button title='v-24-07-09_14:06' onClick={() => {
        rerun();
      }}>Rerun from</button>
      <input
        size={8}
        value={startDate}
        onChange={({ target }) => setStartDate(target.value)}
      />
      {executionVersion && (
        <InnerSimulator 
          key={executionVersion}
          onLoadPlayableTrace={loadPlayableTrace} 
          initialPlayableTrace={playableTrace}
        />
      )}
    </div>
  )
}

const InnerSimulator = ({ initialPlayableTrace, onLoadPlayableTrace }) => {

  const [initialDate, setInitialDate] = useState(contactFlowSimulatorSystem.getCurrentDate());
  const [playableTrace, setPlayableTrace] = useState(initialPlayableTrace);
  const [trace, setTrace] = useState([
    { 
      type: 'init-date', 
      date: contactFlowSimulatorSystem.getCurrentDate() 
    }
  ]);
  const [candidateState, setCandidateState] = useState<{
    isOptimized: boolean;
    agencyType: AgencyType;
    isCDII: boolean;
    contracts: Contract[];
    siretStates: CandidateState['siretStates'];
    series: Serie[];
    projects: CandidateState['projects'];
    payments: unknown[];
    lastTouchpointDate: string; 
    lastAnswerDate: string; 
    lastActivationDate?: string;
    nbSuccessiveNonReplies?: number;
    version: string;
  }>({
    isOptimized: true,
    agencyType: (window.location?.href || '').indexOf('inside-staffing') >= 0 ? 'is-agency' as const : 'city-agency' as const,
    isCDII: false,
    contracts: [],
    projects: [],
    payments: [],
    series: [],
    siretStates: {},
    lastTouchpointDate: '2023-01-01', 
    lastAnswerDate: '2023-01-01', 
    version: getRandomString(12),
  });
  const [timeline, setTimeline] = useState([]);
  const [currentDate, setCurrentDate] = useState(contactFlowSimulatorSystem.getCurrentDate());
  const [nextActionDate, setNextActionDate] = useState('2023-01-01');
  const [contactFlowDescriptor, setContactFlowDescriptor] = useState(
    baseContactFlowDescriptor
  );
  const [shownContractIds, setShownContractId] = useState([]); 
  const [areActionsShown, setAreActionsShown] = useState(true);
  const [calendarShownEvents, setCalendarShownEvents] = useState([]); 
  const [droppedRollingTasks, setDroppedRollingTasks] = useState<RollingTask[]>([])
  

  const onSelectContractForDisplay = ({ contractId }) => {
    setShownContractId([contractId]);
  }
  const onUnselectContractForDisplay = ({ contractId }) => {
    setShownContractId([]);
  }

  useEffect(() => {
    const newCalendarShownEvents = [];
    // Contracts key events
    const contracts = candidateState.contracts || [];
    for (const contractId of (shownContractIds || [])) {
      const contract = _.findWhere(contracts, { id: contractId });
      const firstStartDate = getContractFirstStartEventDate({ contract, contracts });
      if (firstStartDate) {
        const eventId = `${contract.id}_start-1`;
        newCalendarShownEvents.push({ id: eventId, date: firstStartDate, title: 'DM',color: 'lime' });
      }
      const secondStartDate = getContractSecondStartEventDate({ contract, contracts });
      if (secondStartDate) {
        const eventId = `${contract.id}_start-2`;
        newCalendarShownEvents.push({ id: eventId, date: secondStartDate, title: 'DM',color: 'green' });
      }
      const firstEndDate = getContractFirstEndEventDate({ contract, contracts });
      if (firstEndDate) {
        const eventId = `${contract.id}_end-1`;
        newCalendarShownEvents.push({ id: eventId, date: firstEndDate, title: 'FM',color: 'blue' });
      }
      const secondEndDate = getContractSecondEndEventDate({ contract, contracts });
      if (secondEndDate) {
        const eventId = `${contract.id}_end-2`;
        newCalendarShownEvents.push({ id: eventId,  date: secondEndDate, title: 'FM',color: 'cyan' });
      }
      const followupEvents = getContractFollowupEvents({
        contract,
        startEvents: _.map(_.compact([firstStartDate, secondStartDate]), (d) => ({ date: d })),
        endEvents: _.map(_.compact([firstEndDate, secondEndDate]), (d) => ({ date: d })),
        currentDate
      });
      _.each(followupEvents, (ev, index) => {
        const eventId = `${contract.id}_followup-${index+1}`;
        newCalendarShownEvents.push({ id: eventId,  date: ev.date, title: 'SM', color: 'magenta' });
      });
    }
     // Scheduled Actions
    if (areActionsShown) {
      const colorFromContractId = {};
      _.each(candidateState.contracts, (contract, index) => {
        colorFromContractId[contract.id] = getColorFromIndex({ index });
      })
      const currentSequenceDescriptor = _.find(
        contactFlowDescriptor.sequences,
        seq => seq.isCurrent && !seq.completion?.isCompleted
      );
      for (const action of currentSequenceDescriptor?.actions || []) {
        if (!action.completion?.isCompleted) {
          const refDate = action.trigger?.date;
          const date = new Date(
            (refDate && refDate > currentDate) ? refDate : currentDate
          ).toISOString().slice(0,10) || '';
          for (const unitEvent of action.metadata?.unitEvents || []) {
            newCalendarShownEvents.push({ 
              id: `${action.id}_${unitEvent.contractId || (unitEvent.contractIds || []).join()}_${unitEvent.type}`,  
              date, 
              title: `${codeFromEventType[unitEvent.type]}`,
              underlined: true,
              color: colorFromContractId[unitEvent.contractId] || 'red', 
            });
          }            
        }
      }
    }
    setCalendarShownEvents(newCalendarShownEvents);
  }, [currentDate, shownContractIds, candidateState.contracts, areActionsShown, contactFlowDescriptor])


  const logTraceItem = (item) => {

    const state = {
      initialDate,
      candidateState,
      timeline,
      currentDate,
      nextActionDate,
      contactFlowDescriptor
    };

    setTrace(trace => [
      ...trace.slice(0,-1),
      { ..._.last(trace), state }, 
      item
    ]);
  }

  const playTraceItem = async ({ traceItem }) => {
    if (!traceItem?.type) {
      throw Error('"type" manquant');
    }

    if (traceItem.type === 'go-do-date' || traceItem.type === 'go-to-date') {
      if (!traceItem.date) {
        throw Error('"date" manquante pour go-to-date');
      }
      onGoToDay(traceItem.date);
    }  
    if (traceItem.type === 'manual-activity') {
      addToTimeline({
        source: 'manual',
        type: traceItem.activityType,
        date: traceItem.date,
        text: traceItem.text, 
      })
    }
    if (traceItem.type === 'set-cdii-value') {
      onChangeIsCDII({ isCDII: traceItem.value === 'cdii' })
    }
    if (traceItem.type === 'create-contract') { //contract
      if (!traceItem.contract?.id) {
        throw Error('contrat invalide dans la trace');
      }
      onCreateContract({
        idInTrace: traceItem.contract.id, 
        title: traceItem.contract.title, 
        startDate: traceItem.contract.startDate, 
        endDate: traceItem.contract.endDate,
        expectedEndDate: traceItem.contract.expectedEndDate,
        realEndDate: traceItem.contract.realEndDate,
        qualification: traceItem.contract.qualification, 
        siret: traceItem.contract.siret, 
        addendums: traceItem.contract.addendums,
      });
    }
    if (traceItem.type === 'update-contract') {
      if (!traceItem.contract?.id) {
        throw Error('contrat invalide dans la trace');
      }
      onUpdateContract({ 
        id: traceItem.contract.id, 
        title: traceItem.contract.title, 
        startDate: traceItem.contract.startDate, 
        endDate: traceItem.contract.endDate, 
        expectedEndDate: traceItem.contract.expectedEndDate,
        realEndDate: traceItem.contract.realEndDate,
        qualification: traceItem.contract.qualification, 
        siret: traceItem.contract.siret, 
        addendums: traceItem.contract.addendums,
      });
    }
    if (traceItem.type === 'delete-contract') {
      if (!traceItem.contractId) {
        throw Error('id de contrat manquant dans la trace');
      }
      onDeleteContract({ id: traceItem.contractId });
    }
  
    if (traceItem.type === 'create-pool') {
      if (!traceItem.pool?.id) {
        throw Error('vivier invalide dans la trace');
      }
      onCreateProject({ 
        idInTrace: traceItem.pool.id,
        title: traceItem.pool.title,
        type: traceItem.pool.type,
      })
    }

    if (traceItem.type === 'update-pool') {
      if (!traceItem.pool?.id) {
        throw Error('vivier invalide dans la trace');
      }
      onUpdateProject({ 
        id: traceItem.pool.id,
        title: traceItem.pool.title,
        type: traceItem.pool.type,
      })
    }

    if (traceItem.type === 'delete-pool') {
      if (!traceItem.poolId) {
        throw Error('poolId manquant dans la trace');
      }
      onDeleteProject({ 
        id: traceItem.poolId
      });
    }   

    if (traceItem.type === 'assertion') {
      logTraceItem(traceItem);
      const header = (
        traceItem.title ? '# ' + traceItem.title + '\n' : '' 
      ) + (
        traceItem.comment ? traceItem.comment + '\n' : ''
      );
      const assertionValidator = getAssertionValidator({ 
        assertion: traceItem
      });
      try {
        assertionValidator.assert({
          candidateState,
          contactFlowDescriptor,
          currentDate,
        });
        alert((header ? header + '\n' : '') + 'Checkpoint ok');
      } catch (e) {
        alert((header ? header + '\n' : '') + e.message);
      }
      
    }

    if (traceItem.type === 'comment') {
      logTraceItem(traceItem);
      alert(`Commentaire: ${traceItem.comment}`);
    }

    if (traceItem.type === 'perform-action') {
      const contactFlow = contactFlowSimulatorSystem.createContactFlow(contactFlowDescriptor);
      const currentSequence = contactFlow.getCurrentSequence();
      if (!currentSequence) {
        throw Error('no action to perform');
      }

      let nextActionId = null;

      let lastActionIsCompleted = true;
      for (const actionDescriptor of currentSequence.getDescriptor()?.actions || []) {
        const action = currentSequence.getAction({ id: actionDescriptor.id });        
        if (
          !action.getIsCompleted() && 
          lastActionIsCompleted && 
          (
            actionDescriptor.trigger?.type === 'manual-trigger' ||
            await action.getTrigger().accept({ context: {} })
          )
        ) {
          if (!nextActionId) {
            nextActionId = action.getId();
          }
        }
        lastActionIsCompleted = action.getIsCompleted();
      }
      onExecuteAction({ 
        actionId: nextActionId, 
        type: traceItem.activityType
      });
    }
  }

  const playNextTraceItem = () => {
    const nextPlayableIndex = _.findIndex(playableTrace, ({ isPlayed }) => !isPlayed);

    if (nextPlayableIndex >= 0) {
      const playableTraceItem = playableTrace[nextPlayableIndex];
      try {
        playTraceItem({ traceItem: playableTraceItem });
        setPlayableTrace(
          _.map(playableTrace, (playableTraceItem, index) => (
            index === nextPlayableIndex ? {
              ...playableTraceItem,
              isPlayed: true
            } : playableTraceItem
          ))
        );  
      } catch (e) {
        alert(e.message);
      }
      
    } else {
      alert('Toutes les étapes ont été jouées')
    }
  }

  const restoreState = ({ state, prefixTrace }) => {
    contactFlowSimulatorSystem.setCurrentDate(state.currentDate);
    setTrace(prefixTrace);
    setInitialDate(state.initialDate);
    setCandidateState(state.candidateState);
    setTimeline(state.timeline);
    setCurrentDate(state.currentDate);
    setNextActionDate(state.nextActionDate);
    setContactFlowDescriptor(state.contactFlowDescriptor);
  }

  const addToTimeline = (timelineItem) => {
    if (timelineItem.source === 'manual') {
      logTraceItem({ 
        type: 'manual-activity', 
        activityType: timelineItem.type, 
        date: timelineItem.date, 
        text: timelineItem.text, 
      });
    }
    setTimeline(timeline => [...timeline, { id: getRandomString(12), ...timelineItem } ]);
    let newLastTouchpointDate = timelineItem.date >= candidateState.lastTouchpointDate ? (
      timelineItem.date
    ) : null;
    let newLastAnswerDate = (
      _.contains(['discussion', 'from-profile'], timelineItem.type) && 
      timelineItem.date >= candidateState.lastAnswerDate
    ) ? (
      timelineItem.date
    ) : null; 
    if (newLastTouchpointDate || newLastAnswerDate) {
      setCandidateState(candidateState => ({
        ...candidateState,
        ...newLastTouchpointDate && {
          lastTouchpointDate: newLastTouchpointDate
        },
        ...newLastAnswerDate && { 
          lastAnswerDate: newLastAnswerDate
        },
        ...(
          timelineItem.source === 'manual' && newLastAnswerDate &&
          (!candidateState.lastIntermissionTouchpointDate || timelineItem.date > candidateState.lastIntermissionTouchpointDate)
        ) && {
          lastIntermissionTouchpointDate: timelineItem.date
        },
        version: getRandomString() // recompute
      }));
    }
  }

  const updateContactFlowDescriptor = ({ newContactFlowDescriptor }) => {
    try {
      const contactFlow = contactFlowSimulatorSystem.createContactFlow(newContactFlowDescriptor);
      setContactFlowDescriptor(contactFlow.getDescriptor());
      const updateOfNextActionDate = async () => {
        const nextActionDate = await contactFlow.getNextActionDate({ context: {} });
        setNextActionDate(new Date(nextActionDate).toISOString().slice(0,10))
      }
      updateOfNextActionDate();
    } catch (e) {
      console.log(e);
    }
  }

  const recomputeContactFlowDescriptor = () => {
    const { updatedContactFlowDescriptor, emittedEvents } =  getSmartlyUpdatedCFDAndEmittedEvents({ 
      contactFlowSimulatorSystem,
      contactFlowDescriptor, 
      candidateState: candidateState,
    });

    if (!_.isEmpty(emittedEvents)) {
      const stateAfterReduction = getCandidateStateAfterReduction({
        candidateState : candidateState,
        emittedEvents,
      });
      const newDrops = _.compact(_.pluck(emittedEvents, 'droppedAction'));
      if (!_.isEmpty(newDrops)) {
        setDroppedRollingTasks(current => [...current, ...newDrops ]);
      }
      setCandidateState(stateAfterReduction);
    }

    //console.log('EMITTED', { emittedEvents });
    updateContactFlowDescriptor({ newContactFlowDescriptor: updatedContactFlowDescriptor });
  }

  const addAssertion = (assertion) => {
    logTraceItem({ 
      type: 'assertion', 
      ...assertion,
    });
  }

  const addComment = (comment) => {
    logTraceItem({ 
      type: 'comment', 
      ...comment,
    });
  }

  const onChangeIsCDII = ({ isCDII }) => {
    console.log({ candidateState });
    setCandidateState((candidateState) => ({
      ...candidateState,
      isCDII,
      version: getRandomString(), // recompute
    }));
  }

  const onExecuteAction = async ({ actionId, type }) => {

    logTraceItem({ 
      type: 'perform-action', 
      activityType: type, 
    });

    const contactFlow = contactFlowSimulatorSystem.createContactFlow(contactFlowDescriptor);
    const currentSequence = contactFlow.getCurrentSequence();
    const action = currentSequence && currentSequence.getAction({ id: actionId });

    if (action) {
      const subSiretStates : Record<string, SiretCandidateState> = {};

      let achievedIntermissionFollowup = false;
      let achievedIntermissionFollowupIndex = 0;
      for (const achievement of action.actionDescriptor?.metadata?.achievements || []) {
        const { siret, type, followupIndex } = achievement;

        if (siret) {
          if (!subSiretStates[siret]) {
            subSiretStates[siret] = { 
              lastCompletedEvent: {
                date: currentDate,
              }
            };
          }
          if (type === 'start-1') {
            subSiretStates[siret].lastCompletedStartEvent = {
              date: currentDate,
            }
          }
          if (type === 'followup') {
            subSiretStates[siret].lastCompletedFollowupEvent = {
              date: currentDate,
              index: followupIndex ?? 0,
            }
          }
        }
        if (achievement.type === 'intermission-followup') {
          achievedIntermissionFollowup = true;
          achievedIntermissionFollowupIndex = followupIndex ?? 1;
        }
      }

      //const metaData = ;
      const mainEvent = action.actionDescriptor?.metadata?.unitEvents?.[0];
      const isIntermissionFollowup = mainEvent?.type === "intermission-followup";
      console.log('UB', { action, isIntermissionFollowup });
      
      
     
      const hasReplied = type === 'discussion';
      //const newDescriptor = resultingContactFlow.getDescriptor();
      const newNbSuccessiveNonReplies = !isIntermissionFollowup || hasReplied ? (
        0
      ) : (
        1 + (candidateState.nbSuccessiveNonReplies || 0)
      ); 
      setCandidateState(candidateState => ({
        ...candidateState,
        siretStates: mergeCandidateSiretStates(candidateState.siretStates, subSiretStates),
        ...achievedIntermissionFollowup && {
          lastCompletedInterserieFollowupEvent: {
            date: currentDate,
            index: achievedIntermissionFollowupIndex ?? 0,
          },
          lastIntermissionTouchpointDate: currentDate,
        },
        lastTouchpointDate: contactFlowSimulatorSystem.getCurrentDate(),
        nbSuccessiveNonReplies: newNbSuccessiveNonReplies,
        version: getRandomString(), // recompute
      }));
      addToTimeline({ 
        type,
        date: contactFlowSimulatorSystem.getCurrentDate(), 
        text: {
          'discussion' : 'Discussion',
          'from-profile': 'Message reçu',
          'to-profile': 'Message/Appel émis', 
        }[type]
      });
      if (newNbSuccessiveNonReplies >= 3 && !candidateState.isCDII) {
        alert('Notification pour sortie de vivier (non CDII)')
      }
    } else {
      alert('no next action!');
    }
  }

  const onGoToDay = async (day) => {
    const newValue = new Date(
      day
    ).toISOString().slice(0,10);
    logTraceItem({ type: 'go-to-date', date: newValue });
    contactFlowSimulatorSystem.setCurrentDate(newValue);
    setCurrentDate(newValue);
    recomputeContactFlowDescriptor();
  }

  const onCreateContract = ({ idInTrace, title, startDate, endDate, expectedEndDate, realEndDate, qualification, siret, addendums }) => {
    const newContract = { 
      creationDate: currentDate,
      id: idInTrace || getRandomString(12), 
      title, 
      startDate, 
      endDate, 
      expectedEndDate, 
      realEndDate, 
      qualification, 
      siret, 
      addendums 
    };
    const isCollectable = isContractCollectable({ 
      contract: newContract, 
      lastActivationDate: candidateState.lastActivationDate,
      currentDate 
    });
    const newContracts = [
      ...(candidateState.contracts || []),
      { 
        ...newContract,
        isCollectable
      },
    ];
    const newSeries = getSeriesFromContracts({ 
      agencyType: candidateState.agencyType, 
      contracts: newContracts,
    });
    console.log({ newSeries });
    const newCandidateState = {
      ...candidateState,
      contracts: newContracts,
      series: newSeries,
      version: getRandomString(), // recompute
    }
    logTraceItem({ type: 'create-contract', contract: newContract });
    setCandidateState(newCandidateState);
  }

  const onUpdateContract = ({ id, title, qualification, siret, startDate, endDate, expectedEndDate, realEndDate, addendums }) => {
    const updatedContract = { id, title, qualification, siret, startDate, endDate, expectedEndDate, realEndDate, addendums };
    const newContracts = _.map(candidateState.contracts || [], (contract) => (
      contract.id !== id ? (
        contract
      ) : (
        { 
          ...contract, 
          ...updatedContract,
          isCollectable: contract.isCollectable && (
            isContractCollectable({ contract: updatedContract, currentDate })
          )
        }
      )
    ));
    const newSeries = getSeriesFromContracts({ 
      agencyType: candidateState.agencyType, 
      contracts: newContracts 
    });
    console.log({ newSeries });
    const newCandidateState = {
      ...candidateState,
      contracts: newContracts,
      series: newSeries,
      version: getRandomString(), // recompute
    };
    logTraceItem({ type: 'update-contract', contract: updatedContract });
    setCandidateState(newCandidateState);
  }

  const onDeleteContract = ({ id }) => {
    const newContracts = _.filter(candidateState.contracts || [], (contract) => (
      contract.id !== id
    ));
    const newSeries = getSeriesFromContracts({ 
      agencyType: candidateState.agencyType, 
      contracts: newContracts 
    });
    console.log({ newSeries });
    const newCandidateState = {
      ...candidateState,
      contracts: newContracts,
      series: newSeries,
      version: getRandomString(), // recompute
    }
    logTraceItem({ type: 'delete-contract', contractId: id });
    setCandidateState(newCandidateState);
  }

  const isActivatingProjectsUpdate = ({ oldProjects, newProjects }) => {
    const wasActivated = _.some(oldProjects, project => project.type !== 'sleeping');
    const isNowActivated = _.some(newProjects, project => project.type !== 'sleeping');
    return !wasActivated && isNowActivated;
  }

  const onCreateProject = ({ idInTrace, title, type }) => {
    const newProject = { id: idInTrace || getRandomString(12), title, type };
    const newProjects = [
      ...(candidateState.projects || []),
      newProject,
    ];
    const newCandidateState = {
      ...candidateState,
      ..._.isEmpty(candidateState.projects) && {
        lastTouchpointDate: currentDate, 
        lastAnswerDate: currentDate,
      },
      projects: newProjects,
      ...isActivatingProjectsUpdate({ oldProjects: candidateState.projects, newProjects }) && {
        lastActivationDate: currentDate,
        lastTouchpointDate: currentDate,
      },
      version: getRandomString(), // recompute
    }
    logTraceItem({ type: 'create-pool', pool: newProject });
    setCandidateState(newCandidateState);
  }

  const onUpdateProject = ({ id, title, type }) => {
    const updatedProject = { id, title, type };
    const newProjects = _.map(candidateState.projects || [], (project) => (
      project.id !== id ? (
        project
      ) : (
        { ...project, ...updatedProject }
      )
    ));
    const newCandidateState = {
      ...candidateState,
      projects: newProjects,
      ...isActivatingProjectsUpdate({ oldProjects: candidateState.projects, newProjects }) && {
        lastActivationDate: currentDate,
        lastTouchpointDate: currentDate,
      },
      version: getRandomString(), // recompute
    }
    logTraceItem({ type: 'update-pool', pool: updatedProject });
    setCandidateState(newCandidateState);
  }

  const onDeleteProject = ({ id }) => {
    const newCandidateState = {
      ...candidateState,
      projects: _.filter(candidateState.projects || [], (project) => (
        project.id !== id
      )),
      version: getRandomString(), // recompute
    }
    logTraceItem({ type: 'delete-pool', poolId: id });
    setCandidateState(newCandidateState);
  }


  /*
  useEffect(() => {
    recomputeContactFlowDescriptor();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  */
  
  useEffect(() => {
    console.log('VI recompute CFD', candidateState.version);
    recomputeContactFlowDescriptor();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [candidateState.version]);
  

  const shouldDisplayTrace = (window.location?.href || '').indexOf('trace') >= 0;

  return (
    <div style={{
      height: '97vh',
      width: '97vw',
      padding: 20,
    }}>
      {!_.isEmpty(playableTrace) && (
        <div>
          <h4 style={{ marginTop: 5, marginBottom: 5 }}>Trace chargée</h4>
          <button
            style={{ marginRight: 10 }}
            onClick={playNextTraceItem}
          >
            Play
          </button>
          <button 
            style={{ marginRight: 10 }}
            onClick={() => setPlayableTrace(null)}
          >
            Stop
          </button>
          <span>{playableTrace.length}</span>
          <br/><br/>
          {_.map(playableTrace, (playableTraceItem, index) => (
            <div 
              key={index} 
              style={{ 
                display: 'inline-block', 
                marginRight: 10, 
                width: 20, 
                height: 20, 
                background: {
                  'go-do-date': '#f7f3b7',//typo
                  'go-to-date': '#f7f3b7',
                  'manual-activity': 'whitesmoke',
                  'perform-action': 'white',
                  'create-contract': 'blue',
                  'update-contract': 'blue',
                  'delete-contract': 'blue',
                  'create-pool': 'pink',
                  'update-pool': 'pink',
                  'delete-pool': 'pink',
                  'set-cdii-value': 'grey',
                }[playableTraceItem.type] || 'white',
                border: playableTraceItem.isPlayed ? '2px solid black' : '1px solid lightgrey',
              }}
            >
              <center>{index+1}</center>
            </div>
          ))}
        </div>        
      )}
      <div>
        {!_.isEmpty(playableTrace) && <h4 style={{ marginTop: 5, marginBottom: 5 }}>Trace courante</h4>}
        {_.map(trace, (traceItem, index) => (
          <div 
            key={index} 
            style={{ 
              display: 'inline-block', 
              width: 20, 
              height: 20, 
              background: index < trace.length -1 ? 'grey' : 'green',
              cursor: index < trace.length -1 ? 'pointer' : 'auto',
              marginRight: 10, 
            }}
            onClick={()=> {
              if (index < trace.length -1) {
                restoreState({ 
                  state: traceItem.state,
                  prefixTrace: trace.slice(0, index+1),
                });
              }
            }}
          >
            <center>{index+1}</center>
          </div>
        ))}
      </div>
      <div 
        style={{
          display: 'inline-block',
          verticalAlign: 'top',
          height: '90%',
          width: '45%'
        }}
      >
        <h3>Calendrier</h3>
        {/*<h4>NbNonRep: {candidateState.nbSuccessiveNonReplies}</h4>*/}
        <Calendar 
          initialDate={initialDate}
          currentDate={currentDate} 
          nextActionDate={nextActionDate}
          shownEvents={calendarShownEvents}
          onGoToDay={onGoToDay}
        />
      </div>

      <div 
        style={{
          display: 'inline-block',
          verticalAlign: 'top',
          height: '90%',
          width: '23%',
          paddingLeft: 25,
        }}
      >
        <h3>
          Actions{' '}
          <div style={{ display: 'inline-block', paddingLeft: 3 }}>
            <div 
              style={{ 
                width: 10, 
                height: 10, 
                cursor: 'pointer',
                borderRadius: 5,
                border: '1px solid lightgrey',
                backgroundColor: areActionsShown ? 'yellow' : 'white' 
              }}
              onClick={() => {
                setAreActionsShown(value => !value);
              }}
            />
          </div>
        </h3>
        <CurrentSequence
          key={currentDate}
          contactFlowSimulatorSystem={contactFlowSimulatorSystem}
          contactFlowDescriptor={contactFlowDescriptor}
          onExecuteAction={onExecuteAction}
          currentDate={currentDate}
          onSetAreActionsShown={setAreActionsShown}
          areActionsShowns={areActionsShown}
        />
        <br/><br/>
        <h3>Enregistrer une activité</h3>
        <ActivityEditor 
          currentDate={currentDate}
          addToTimeline={addToTimeline}
        />
        {shouldDisplayTrace ? (
          <div>
            <br/><br/>
            <h3>Ajouter un checkpoint</h3>
            <AssertionEditor 
              addAssertion={addAssertion}
            />
          </div>
        ) : ''}
        {shouldDisplayTrace ? (
          <div>
            <br/><br/>
            <h3>Ajouter un commentaire</h3>
            <CommentEditor 
              addComment={addComment}
            />
          </div>
        ) : ''}
        <br/><br/>
        <h3>Historique</h3>
        <Timeline timeline={timeline} />
      </div>
      <div 
        style={{
          display: 'inline-block',
          verticalAlign: 'top',
          height: '90%',
          width: '23%',
          marginLeft: 30,
        }}
      >
        <h3>Type d'agence</h3>
        <h5>{{
          'city-agency': 'Agence de ville',
          'is-agency': 'Agence IS',
        }[candidateState.agencyType]}
        </h5>

        {/*
        <h3>Actions/Fusions optimisées</h3>
        <select 
          value={candidateState.isOptimized ? 'optimized' : 'not-optimized'}
          onChange={e => {
            setCandidateState((current) => ({ 
              ...current,
              isOptimized: e.target.value === 'optimized'
            }));
          }}
        >
          {_.map(
            [ 
              { id: 'optimized', text: 'Optimisées' },
              { id: 'not-optimized', text: 'Non optimisées' },
            ],
            ({ id, text }) => (
              <option key={id} value={id}>{text}</option>
            )
          )}
        </select>
        */}
        <h3>Contrats</h3>
        <ContractsEditor
          contracts={candidateState.contracts || []}
          onSelectContractForDisplay={onSelectContractForDisplay}
          onUnselectContractForDisplay={onUnselectContractForDisplay}
          shownContractIds={shownContractIds}
          onCreateContract={onCreateContract}
          onUpdateContract={onUpdateContract}
          onDeleteContract={onDeleteContract}
          currentDate={currentDate}
          lastActivationDate={candidateState.lastActivationDate}
          series={candidateState.series}
        />
        <br/><br/>
        <h3>State debug</h3>
        <pre>
          {JSON.stringify(_.pick(candidateState, 'lastCompletedInterserieFollowupEvent', 'siretStates', 'rollingTask'), null, 4)}
        </pre>
        <div>Abandons</div>
        {_.map(droppedRollingTasks, (droppedRollingTask, index) => (
          <div key={`${index}_${droppedRollingTask.key}`}>
            {droppedRollingTask.key} - {droppedRollingTask.initialDate} 
          </div>
        ))}
        <br/><br/>
        <h3>Viviers</h3>
        <ProjectsEditor 
          projects={candidateState.projects || []}
          onCreateProject={onCreateProject}
          onUpdateProject={onUpdateProject}
          onDeleteProject={onDeleteProject}
        />
        <br/><br/>
        <h3>Type</h3>
        <select 
          value={candidateState.isCDII ? 'cdii' : 'not-cdii'}
          onChange={e => {
            logTraceItem({ type: `set-cdii-value`, value: e.target.value });
            onChangeIsCDII({ isCDII: e.target.value === 'cdii' })
          }}
        >
          {_.map(
            [ 
              { id: 'cdii', text: 'CDII' },
              { id: 'not-cdii', text: 'Non CDII' },
            ],
            ({ id, text }) => (
              <option key={id} value={id}>{text}</option>
            )
          )}
        </select>
        <br/><br/>
        {shouldDisplayTrace ? (
          <div>
            <h3>Trace</h3>
            <TraceManager 
              trace={trace}
              onLoadPlayableTrace={onLoadPlayableTrace} 
            />
          </div>
        ) : ''}
      </div>

    </div>
  );
}


export default ExternalSimulator;
