import getUpcomingActionsAndEmittedEvents from './rules_v2/getUpcomingActionsAndEmittedEvents.ts';
import getUpdatedCFDWithNewActions from './getUpdatedCFDWithNewActions.ts';
import { ContactFlowDescriptor, EmittedEvent } from '../types.ts';
import { getRandomString } from '../common.js';

const getSmartlyUpdatedCFD = ({
  contactFlowSimulatorSystem,
  contactFlowDescriptor,
  candidateState,
  priority,
}) : {
  updatedContactFlowDescriptor: ContactFlowDescriptor,
  emittedEvents: EmittedEvent[];
} => {

  console.log('COMPUTE - ', getRandomString());

  const currentDate = contactFlowSimulatorSystem.getCurrentDate();
  const { 
    upcomingActions,
    emittedEvents,
  } = getUpcomingActionsAndEmittedEvents({ currentDate, candidateState });

  const updatedContactFlowDescriptor = getUpdatedCFDWithNewActions({
    contactFlowSimulatorSystem,
    contactFlowDescriptor,
    priority,
    upcomingActions
  });

  return {
    updatedContactFlowDescriptor,
    emittedEvents,
  }
}


export default getSmartlyUpdatedCFD;