import ManualAction from "./ManualAction";

const ACTION_CLASS_BY_TYPE = {
  'manual': ManualAction
};

export const getActionFactory = ({ customDateSystem }) => ({
  createAction: ({ sequenceId, actionDescriptor, customDateSystem, trigger }) => {
    const { type } = actionDescriptor;
   
    const ActionClass = ACTION_CLASS_BY_TYPE[type];
    if (!ActionClass) {
      throw TypeError(`Action type ${type} is not defined`);
    }

    return new ActionClass({ sequenceId, actionDescriptor, customDateSystem, trigger });
  },
});

export default getActionFactory;
