import _ from 'underscore';
import { CandidateState, Event, Serie, SiretCandidateState } from "../../../types";
import { getDayAfterBusinessOffset, getDayAfterOffset, getDiffDatesInDays, getFirstBusinessDayEqualOrAfter, getLastBusinessDayEqualOrBefore, getMondayOfNextWeek, getMondayOfWeek } from '../../../common';
import { getMinimalAdmissibleDate, getMinimalSeeableDateOfSerie } from './getMinimalAdmissibleDate.ts';


const getEventsFromSerie = ({
  currentDate,
  candidateState, 
  serie 
} : { 
  currentDate: string;
  candidateState : CandidateState, 
  serie : Serie 
}) : Event[] => {
  
  const siretState : SiretCandidateState = candidateState?.siretStates[serie.siret] || {};

  const startEventsFromSerie = getStartEventsFromSerie({ currentDate, candidateState, serie });
  
  console.log('SIRET', serie.siret);

  const getStartEventRefDate = () : string | undefined => {
    if (siretState?.lastCompletedStartEvent?.date) {
      const diffDays = getDiffDatesInDays(siretState?.lastCompletedStartEvent?.date, serie.startDate);
      if (diffDays <= 5) {
        return siretState.lastCompletedStartEvent.date;
      } 
    }
    return _.last(startEventsFromSerie)?.date;
  }
  const startEventRefDate = getStartEventRefDate();

  console.log('SIRET_SERD', serie.siret, startEventRefDate);

  const endEventsFromSerie = getEndEventsFromSerie({ 
    currentDate, 
    candidateState, 
    serie,
    lastStartEventDate: startEventRefDate, 
  });


  const getFollowupReferenceDateAndIndex = () : { 
    lastFollowupReferenceDate?: string; 
    lastFollowupIndex?: number;
  } => {
    if (siretState.lastCompletedFollowupEvent?.date) {
      const diffDays = getDiffDatesInDays(siretState?.lastCompletedFollowupEvent?.date, serie.startDate);
      if (diffDays <= 5) {
        return {
          lastFollowupIndex: siretState.lastCompletedFollowupEvent.index ?? 0,
          lastFollowupReferenceDate: siretState.lastCompletedFollowupEvent.date,
        };
      } 
    }
    return {};
  }
  const { lastFollowupReferenceDate, lastFollowupIndex }  = getFollowupReferenceDateAndIndex();
  const serieFollowupEvents = getSerieFollowupEvents({ 
    currentDate,
    candidateState,
    serie,
    followupIntervalStartDate: _.last(_.sortBy([
      getMondayOfNextWeek(startEventRefDate ?? serie.startDate),
      startEventRefDate ? getDayAfterBusinessOffset(startEventRefDate, 5) : '',
      lastFollowupReferenceDate ? (
        getFirstBusinessDayEqualOrAfter(getDayAfterOffset(
          lastFollowupReferenceDate, 
          (lastFollowupIndex ?? 0) < 2 ? 7 : 14
        ))
      ) : '',
    ])) ?? serie.startDate,
    followupIntervalEndDate: _.sortBy([
      getDayAfterBusinessOffset(getMondayOfWeek(_.first(endEventsFromSerie)?.date ?? serie.endDate),-1),
      getDayAfterBusinessOffset(_.first(endEventsFromSerie)?.date ?? serie.endDate, -2),
    ])[0],
    startIndex: lastFollowupIndex ?? 0,
  });

  return [
    ...startEventsFromSerie,
    ...serieFollowupEvents,
    ...endEventsFromSerie,
  ];
}


export const getStartEventsFromSerie = ({ 
  currentDate,
  candidateState,
  serie 
} : { 
  currentDate: string;
  candidateState: CandidateState,
  serie : Serie 
}): Event[]=> {

  const firstContract = serie.contracts[0];
  if (!firstContract) {
    return [];
  }

  const siretState : SiretCandidateState | undefined = (
    candidateState.siretStates?.[serie.siret]
  );

  for (const otherSerie of candidateState.series) {
    if (otherSerie.startDate < serie.startDate && otherSerie.siret === serie.siret) {
      const diffDays = getDiffDatesInDays(otherSerie.endDate, serie.startDate);
      if (diffDays <= 182) {
        return [];
      }
    }
  }

  if (siretState?.lastCompletedEvent?.date) {
    const diffDays = getDiffDatesInDays(siretState?.lastCompletedEvent?.date, currentDate);
    if (diffDays <= 182) {
      return [];
    }
  }

  const key = `start-1__${serie.siret}`;

  const minDate = getFirstBusinessDayEqualOrAfter(
    _.last(_.sortBy([
      getMinimalAdmissibleDate({ currentDate, candidateState }),
      getMinimalSeeableDateOfSerie({ serie }),
    ]))
  );

  const maxDate = _.sortBy([
    getLastBusinessDayEqualOrBefore(serie.endDate),
    getFirstBusinessDayEqualOrAfter(getDayAfterOffset(serie.startDate, 2))
  ])[0];


  if (minDate <= maxDate) {
    const date = minDate;
    return [
      { 
        type: 'start-1',
        key,
        isMatchingRollingTask: key === candidateState.rollingTask?.key,
        siret: serie.siret,
        contractId: firstContract.id,
        contractTitle: firstContract.title,
        date,
        maxReferenceDueDate: date,
      }
    ];
  } 
  return [];
}


export const getEndEventsFromSerie = ({ 
  currentDate,
  candidateState,
  serie,
  lastStartEventDate
} : { 
  currentDate : string,
  candidateState : CandidateState,
  serie : Serie,
  lastStartEventDate: string | undefined;
}) : Event[] => {
  const firstContract = serie.contracts[0];
  if (!firstContract) {
    return [];
  }
  
  const key = `end-1__${serie.siret}`;

  const minDate : string = _.last(_.sortBy([
    getFirstBusinessDayEqualOrAfter(getMinimalAdmissibleDate({ currentDate, candidateState })),
    getFirstBusinessDayEqualOrAfter(getMinimalSeeableDateOfSerie({ serie })),
    getLastBusinessDayEqualOrBefore(serie.endDate),
    lastStartEventDate ? getDayAfterBusinessOffset(lastStartEventDate, 2) : '',
    lastStartEventDate ? getMondayOfNextWeek(lastStartEventDate) : ''   
  ])) ?? '';

  const maxDate = getFirstBusinessDayEqualOrAfter(
    (lastStartEventDate && getMondayOfWeek(serie.startDate) === getMondayOfWeek(serie.endDate)) ? (
      getMondayOfWeek(serie.startDate) === serie.startDate ? (
        getDayAfterBusinessOffset(serie.endDate, 1)
      ): serie.endDate
    ) : getDayAfterBusinessOffset(serie.endDate, 2)
  );

  
  if (minDate <= maxDate) {
    console.log('FM range', [minDate, maxDate]);
    const date = minDate;
    return [
      { 
        type: 'end-1',
        key,
        isMatchingRollingTask: key === candidateState.rollingTask?.key,
        siret: serie.siret,
        contractId: firstContract.id,
        contractTitle: firstContract.title,
        date,
        maxReferenceDueDate: date,
      }
    ];
  }
  return [];
}

export const getSerieFollowupEvents = ({ 
  currentDate,
  candidateState,
  serie,
  followupIntervalStartDate,
  followupIntervalEndDate,
  startIndex,
} : { 
  currentDate : string;
  candidateState : CandidateState,
  serie : Serie,
  followupIntervalStartDate : string;
  followupIntervalEndDate : string;
  startIndex?: number;
}) : Event[] => {
  const firstContract = serie.contracts[0];
  if (!firstContract) {
    return [];
  }

  const serieFollowupEvents : Event[] = [];
  let date = followupIntervalStartDate;
  let index : number = 1 + (startIndex ?? 0);
  while (date <= followupIntervalEndDate) {
    const key = `followup__${index}__${serie.siret}`;
    serieFollowupEvents.push({
      type: 'followup',
      key,
      isMatchingRollingTask: key === candidateState.rollingTask?.key,
      siret: serie.siret,
      contractId: firstContract.id,
      contractTitle: firstContract.title,
      date,
      maxReferenceDueDate: date,
      followupIndex: index,
    });
    date = getFirstBusinessDayEqualOrAfter(
      getDayAfterOffset(date, index < 2 ? 7 : 14)
    );
    index = index+1;
  }

  return serieFollowupEvents;
}

export default getEventsFromSerie;
