import _ from 'underscore';
import { getContractFirstStartEventDate, getContractSecondStartEventDate } from '../animation/rules_v1/contractStartEvents';
import { getContractFirstEndEventDate, getContractSecondEndEventDate } from '../animation/rules_v1/contractEndEvents';
import getContractMainEvents from '../animation/rules_v1/contractMainEvents';

const actionCategoriesFromType = {
  'start-1': ['unconstraint', 'start-mission', 'start-mission-1'],
  'start-2': ['unconstraint', 'start-mission', 'start-mission-2'],
  'followup': ['unconstraint','followup-mission'],
  'intermission-followup': ['unconstraint','intermission'],
  'end-1': ['unconstraint', 'end-mission', 'end-mission-1'],
  'end-2': ['unconstraint', 'end-mission', 'end-mission-2'],
  'payment-point': ['unconstraint','payment'],
  'collect': ['unconstraint', 'collect'],
}

const getNextActionValidator = ({ assertion }) => {
  return {
    assert: ({ candidateState, contactFlowDescriptor, currentDate }) => {
      const nextAction = getNextAction({ contactFlowDescriptor });

      //console.log('NA', { nextAction });

      if (assertion.existence === 'should-exists' && !nextAction) {
        throw Error('KO: La prochaine action devrait exister');
      }
      if (assertion.existence === 'should-not-exists' && nextAction) {
        throw Error('KO: La prochaine action ne devrait pas exister');
      }
      if ((assertion.minDate || assertion.maxDate) && !nextAction) {
        throw Error('KO: Impossible de comparer la date car aucune action à venir');
      }
      if (assertion.minDate && (nextAction.trigger.date || currentDate).slice(0,10) < assertion.minDate) {
        throw Error('KO: Date de prochaine action < ' + assertion.minDate);
      }
      if (assertion.maxDate && (nextAction.trigger.date || currentDate).slice(0,10) > assertion.maxDate) {
        throw Error('KO: Date de prochaine action > ' + assertion.maxDate);
      }

      if ((assertion.contractId || assertion.actionCategory)) {
        if (!nextAction) {
          throw Error('KO: Impossible de tester car aucune action à venir');
        }
        const testedEvents = {
          'next-action': (nextAction.metadata?.unitEvents || []),
          'next-action-main': (nextAction.metadata?.unitEvents || []).slice(0,1),
          'next-action-secondaries': (nextAction.metadata?.unitEvents || []).slice(1),
        }[assertion.assertionType] || [];
        let hasValidatedEvent = false;
        for (const event of testedEvents) {
          console.log({ event });
          let isEventValidated = true;
          if (assertion.contractId) {
            if (
              event.contractId !== assertion.contractId && 
              !(event.contractIds || []).includes(assertion.contractId)
            ) {
              isEventValidated = false;
            }
          }
          if (assertion.actionCategory) {
            if (!(actionCategoriesFromType[event.type] || []).includes(assertion.actionCategory)) {
              isEventValidated = false;
            }
          }
          if (isEventValidated) {
            hasValidatedEvent = true;
          }
        }
        if (!hasValidatedEvent) {
          console.log({ nextAction });
          throw Error(`KO: La prochaine action ne satisfait pas (${
            _.compact([assertion.contractId,assertion.actionCategory]).join(',')
          })`);
        }
      }
      

    }
  }
}

const getReferenceDateValidator = ({ assertion }) => {
  return {
    assert: ({ candidateState, contactFlowDescriptor }) => {
      if (!assertion.contractId) {
        throw Error('INVALIDE: Ce type de checkpoint nécessite un id de contrat');
      }
      const contracts = candidateState.contracts || [];
      const contract = _.findWhere(contracts, { id: assertion.contractId });
      if (!contract) {
        throw Error(`INVALIDE: Contract ${assertion.contractId} manquant`);
      }

      const mainEvents = getContractMainEvents({
        contract,
        contracts,
        lastActivationDate: candidateState.lastActivationDate,
      })
     
      const testedDate = {
        'reference-date-dm1': _.findWhere(mainEvents?.startEvents, { type: 'start-1'})?.date || '',
        'reference-date-dm2': _.findWhere(mainEvents?.startEvents, { type: 'start-2'})?.date || '',
        'reference-date-fm1': _.findWhere(mainEvents?.endEvents, { type: 'end-1'})?.date || '',
        'reference-date-fm2': _.findWhere(mainEvents?.endEvents, { type: 'end-2'})?.date || '',
      }[assertion.assertionType] ?? 'invalid';
      if (testedDate === 'invalid') {
        throw Error(`INVALIDE: type de référence inconnu`);
      }

      const dateTitle = assertion.assertionType.replace('reference-date-', '').toUpperCase();

      if (assertion.existence === 'should-exists' && !testedDate) {
        throw Error(`KO: ${dateTitle} devrait exister`);
      }
      if (assertion.existence === 'should-not-exists' && testedDate) {
        throw Error(`KO: ${dateTitle} ne devrait pas exister`);
      }
      if ((assertion.minDate || assertion.maxDate) && !testedDate) {
        throw Error(`KO: Impossible de comparer car ${dateTitle} non existante`);
      }
      if (assertion.minDate && testedDate < assertion.minDate) {
        throw Error(`KO: ${dateTitle} < ${assertion.minDate}` );
      }
      if (assertion.maxDate && testedDate > assertion.maxDate) {
        throw Error(`KO: ${dateTitle} > ${assertion.maxDate}`);
      }
    }
  }
}
  
const getSerieSpanValidator = ({ assertion }) => {  
  return {
    assert: ({ candidateState }) => {
      if (!assertion.contractId) {
        throw Error('INVALIDE: Ce type de checkpoint nécessite un id de contrat');
      }
      const contracts = candidateState.contracts || [];
      const contract = _.findWhere(contracts, { id: assertion.contractId });
      if (!contract) {
        throw Error(`INVALIDE: Contract ${assertion.contractId} manquant`);
      }

      console.log('EE', candidateState.series);

      const serie = _.find(candidateState.series, (serie) => (
        _.some(serie.contracts, ({ id }) => id === assertion.contractId)
      ));

      console.log('EK', serie);

      if (!serie) {
        throw Error(`INVALIDE: Pas de série trouvée pour ${assertion.contractId}`);
      }
      if (assertion.minDate && serie.startDate.slice(0,10) !== assertion.minDate.slice(0,10)) {
        throw Error(`KO: Date de début de série invalide - ${serie.startDate.slice(0,10)} au lieu de ${assertion.minDate.slice(0,10)}`, );
      }
      if (assertion.maxDate && serie.endDate.slice(0,10) !== assertion.maxDate.slice(0,10)) {
        throw Error(`KO: Date de find de série invalide - ${serie.endDate.slice(0,10)} au lieu de ${assertion.maxDate.slice(0,10)}`, );
      }
    }
  }
}

const getAssertionValidator = ({ assertion }) => {

  if (assertion?.assertionType === 'serie-span') {
    return getSerieSpanValidator({ assertion })
  }

  if (['next-action', 'next-action-main', 'next-action-secondaries'].includes(assertion?.assertionType)) {
    return getNextActionValidator({ assertion });
  }

  if (['reference-date-dm1','reference-date-dm2', 'reference-date-fm1','reference-date-fm2'].includes(assertion?.assertionType)) {
    return getReferenceDateValidator({ assertion });
  }

  return {
    assert : ({ candidateState, contactFlowDescriptor, currentDate }) => {
      throw Error('type de checkpoint non implémenté');
    }
  }
}


const getNextAction = ({ contactFlowDescriptor }) => {
  const currentSequenceDescriptor = _.find(
    contactFlowDescriptor.sequences,
    seq => seq.isCurrent && !seq.completion?.isCompleted
  );
  let nextAction = null;
  for (const action of currentSequenceDescriptor?.actions || []) {
    if (!action.completion?.isCompleted) {
      if (!nextAction || action.trigger?.date < nextAction.trigger?.date) {
        nextAction = action;
      }
    }
  }
  return nextAction;
}

export default getAssertionValidator;