import _ from 'underscore';
import contactFlowManagerFactory from 'sweetapp-contactflow';
import getActionFactory from './actions/actionsFactory';
import conditionsFactory from './conditions/conditionsFactory';

//export const actionFactory = getActionFactory();

export const updateContactFlowAction = ({ contactFlowDescriptor, sequenceId, actionId, updatedActionDescriptor }) => {
  const newSequencesDescriptors = _.map(contactFlowDescriptor.sequences, (sequenceDescriptor) => {
    if (sequenceDescriptor.id !== sequenceId) {
      return sequenceDescriptor;
    }
    const newActionsDescriptors = _.map(sequenceDescriptor.actions, (actionDescriptor) => {
      if (actionDescriptor.id !== actionId) {
        return actionDescriptor;
      }
      return updatedActionDescriptor;
    });
    return {
      ...sequenceDescriptor,
      shouldFollowJobUpdates: false,
      actions: newActionsDescriptors,
    };
  });

  return {
    ...contactFlowDescriptor,
    sequences: newSequencesDescriptors,
  };
};

export const removeContactFlowAction = ({ contactFlowDescriptor, sequenceId, actionId }) => {
  const newSequencesDescriptors = _.map(contactFlowDescriptor.sequences, (sequenceDescriptor) => {
    if (sequenceDescriptor.id !== sequenceId) {
      return sequenceDescriptor;
    }
    const newActionsDescriptors = _.compact(
      _.map(sequenceDescriptor.actions, (actionDescriptor) => {
        if (actionDescriptor.id !== actionId) {
          return actionDescriptor;
        }
        return null;
      }),
    );
    const sequenceLastActionCompletion = (_.last(newActionsDescriptors) || {}).completion || {};
    if (sequenceLastActionCompletion.isCompleted) {
      return {
        ...sequenceDescriptor,
        actions: newActionsDescriptors,
        completion: {
          isCompleted: true,
          completionDate: sequenceLastActionCompletion.completionDate,
          reasons: [
            {
              type: 'last-remaining-action-completed',
              date: sequenceLastActionCompletion.completionDate,
            },
          ],
        },
      };
    }
    return {
      ...sequenceDescriptor,
      actions: newActionsDescriptors,
    };
  });

  return {
    ...contactFlowDescriptor,
    sequences: newSequencesDescriptors,
  };
};

export const addContactFlowAction = ({ contactFlowDescriptor, sequenceId, actionDescriptor }) => {
  const newSequencesDescriptors = _.map(contactFlowDescriptor.sequences, (sequenceDescriptor) => {
    if (sequenceDescriptor.id !== sequenceId) {
      return sequenceDescriptor;
    }
    return {
      ...sequenceDescriptor,
      actions: [...sequenceDescriptor.actions, actionDescriptor],
    };
  });

  return {
    ...contactFlowDescriptor,
    sequences: newSequencesDescriptors,
  };
};


const getContactFlowSimulatorSystem = () => {

  let currentDate = new Date().toISOString().slice(0,10);
  
  const customDateSystem = {
    now: () => new Date(currentDate + (new Date().toISOString()).slice(10)).getTime()
  }

  const actionFactory = getActionFactory({ customDateSystem });

  const { 
    createContactFlow 
  } = contactFlowManagerFactory({ 
    conditionsFactory, 
    actionFactory,
    customDateSystem, 
  });
  

  return {
    customDateSystem,
    getCurrentDate: () => currentDate,
    setCurrentDate : (newCurrentDate) => {
      currentDate = newCurrentDate;
    },
    createContactFlow,
  };

}

export default getContactFlowSimulatorSystem;