import {  ContactFlowSimulatorSystem, ContactFlowDescriptor } from "../types";

const getSmartlyUpdatedContactFlowWhenNoProject = ({
  contactFlowSimulatorSystem,
  contactFlowDescriptor,
} : {
  contactFlowSimulatorSystem : ContactFlowSimulatorSystem;
  contactFlowDescriptor : ContactFlowDescriptor;
}) : ContactFlowDescriptor => {
  const contactFlow = contactFlowSimulatorSystem.createContactFlow(contactFlowDescriptor);
  const newContactFlow = contactFlow.getResultingContactFlowAfterEvent({ 
    event: {
      type: 'switch-to-idle-animation',
      date: new Date(contactFlowSimulatorSystem.customDateSystem.now()),
    }
  });
  return newContactFlow.getDescriptor();
}

export default getSmartlyUpdatedContactFlowWhenNoProject;