import { Action, CandidateState, EmittedEvent, RollingTask } from "../../types.ts";
import getKeyEvents from './keyEvents/getKeyEvents.ts';
import { getActionFromEvent } from "./action/getActionFromEvent.ts";


const getUpcomingActionsAndEmittedEvents = ({
  candidateState,
  currentDate,
} : {
  candidateState: CandidateState;
  currentDate: string;
}) : {
  upcomingActions: Action[];
  emittedEvents: EmittedEvent[];
} => {
  const keyEvents = getKeyEvents({ 
    currentDate, 
    candidateState
  });

  const upcomingActions : Action[] = [];
  
  for (const event of keyEvents) {
    upcomingActions.push(getActionFromEvent({ event }));
  }

  const emittedEvents : EmittedEvent[] = [];

  if (keyEvents[0]?.date <= currentDate) {
    const newRollingTask : RollingTask = {
      type: keyEvents[0].type,
      key: keyEvents[0].key,
      initialDate: currentDate,
    };
    emittedEvents.push({
      type: 'new-rolling-task',
      ...candidateState.rollingTask?.key !== newRollingTask.key && {
        droppedAction: candidateState.rollingTask,
      },
      newAction: {
        type: keyEvents[0].type,
        key: keyEvents[0].key,
        initialDate: currentDate,
      }
    });
  }

  return {
    upcomingActions,
    emittedEvents,
  };
}

export default getUpcomingActionsAndEmittedEvents;
