import _ from 'underscore';
import { ContactFlowDescriptor, ContactFlowSimulatorSystem } from "../types";

const getTimeTriggerUpdatedCFD = ({
  contactFlowSimulatorSystem,
  contactFlowDescriptor
} : {
  contactFlowSimulatorSystem : ContactFlowSimulatorSystem;
  contactFlowDescriptor : ContactFlowDescriptor;
}) : ContactFlowDescriptor => {
  const contactFlow = contactFlowSimulatorSystem.createContactFlow(contactFlowDescriptor);
  
  const currentSequence = contactFlow.getCurrentSequence();
  if (!currentSequence) {
    return contactFlowDescriptor;
  }
  
  const currentSequenceDescriptor = currentSequence.getDescriptor();

  const nextAction = _.find(
    currentSequence.getDescriptor()?.actions,
    ({ completion }) => !completion?.isCompleted
  );

  if (nextAction?.trigger?.type === 'after-date') {
    const date = (nextAction.trigger.date ?? '').slice(0,10);
    const currentDate = contactFlowSimulatorSystem.getCurrentDate();
    if (date <= currentDate) {
      const result = {
        ...contactFlowDescriptor,
        sequences: _.map(contactFlowDescriptor.sequences, (sequence) => (
          sequence.id === currentSequenceDescriptor.id ? ({
            ...sequence,
            actions: _.map(currentSequenceDescriptor.actions, (action) => ({
              ...action,
              ...action.id === nextAction.id && { 
                trigger: { type: 'manual-trigger' as const }
              }
            }))
          }) : sequence
        ))
      };
      return result;
    }
  }
  return contactFlowDescriptor; 
}

export default getTimeTriggerUpdatedCFD;