import _ from 'underscore';
import { 
  getDayAfterBusinessOffset, 
  getDayAfterTransformPolicy, 
  getDiffDatesInDays, 
  getFirstBusinessDayEqualOrAfter 
} from "../../common";
import metaParameters from "../metaParameters"
import { getContractPeriodLength, getContractSmartEndDate } from "./helpers"


const getContractEndEventDate = ({ contract, eventId }) => {
  const refPeriodLength = getContractPeriodLength(
    contract, 
    metaParameters.contractEndEvents.referencePeriodForEventShifts
  );

  const mode = refPeriodLength >= metaParameters.minNbDaysForBeingLongPeriod ? 'long' : 'short';

  const { 
    isDisabled, 
    offsetFromEnd 
  } = metaParameters.contractEndEvents[eventId].behaviourByMode[mode];
  
  if (isDisabled) {
    return null;
  }

  const smartEndDate = getContractSmartEndDate({ contract });

  const preResult = getDayAfterTransformPolicy(smartEndDate, offsetFromEnd);

  return getFirstBusinessDayEqualOrAfter(preResult >= contract.startDate ? preResult : contract.startDate);
}

export const getContractFirstEndEventDate = ({ contract }) => {
  return getContractEndEventDate({ contract, eventId: 'end-1' })
}

export const getContractSecondEndEventDate = ({ contract }) => {
  return getContractEndEventDate({ contract, eventId: 'end-2' })
}


export const getEndEventsFromContract = ({ contract, contracts }) => {

  const firstEndDate = getContractFirstEndEventDate({ contract, contracts });
  const secondEndDate = getContractSecondEndEventDate({ contract, contracts });
 
  const lastEndEventsCompletionDates = [
    ..._.compact([contract.firstEndCompletionDate]),
    ..._.compact([contract.secondEndCompletionDate]),
  ].sort();

  const lastEndCompletionDate = !_.isEmpty(lastEndEventsCompletionDates) ? (
    _.last(lastEndEventsCompletionDates)
  ) : null;

  const minimalDate1 = lastEndCompletionDate
    ? getDayAfterBusinessOffset(lastEndCompletionDate, 5)
    : null;

  const minimalDate2 = contract.secondEndCompletionDate
    ? _.last(
        _.sortBy([
          getDayAfterBusinessOffset(lastEndCompletionDate, 2),
          getDayAfterBusinessOffset(contract.secondEndCompletionDate, 5),
        ])
      )
    : lastEndCompletionDate
    ? getDayAfterBusinessOffset(lastEndCompletionDate, 2)
    : null;  

  const firstEndEvent = firstEndDate && (!minimalDate1 || firstEndDate >= minimalDate1) ? {
    contractId: contract.id,
    contractName: contract.name,
    type: 'end-1',
    date: firstEndDate,
    maxReferenceDueDate: firstEndDate,
  } : null;

  const secondEndEvent = secondEndDate && (!minimalDate2 || secondEndDate >= minimalDate2) ? {
    contractId: contract.id,
    contractName: contract.name,
    type: 'end-2',
    date: secondEndDate,
    maxReferenceDueDate: secondEndDate,
  } : null;

  if (firstEndEvent && secondEndEvent && firstEndEvent.date === secondEndEvent.date) {
    return [firstEndEvent];
  }

  return [
    ..._.compact([firstEndEvent]),
    ..._.compact([secondEndEvent]),
  ];

}
