import _ from 'underscore';
import { getRandomString } from '../../common';
import React, { useState } from 'react';
import { getContractSmartEndDate } from '../../animation/rules_v1/helpers';
import getContractMainEvents from '../../animation/rules_v1/contractMainEvents';

const NB_MS_IN_A_DAY = 24*3600*1000;

const ContractEditor = ({ baseContract, onLeaveEditionMode, onSave }) => {
  const [contractState, setContractState] = useState({
    id: baseContract.id,
    title: baseContract.title,
    siret: baseContract.siret,
    qualification: baseContract.qualification,
    startDate: baseContract.startDate,
    endDate: baseContract.endDate,
    expectedEndDate: baseContract.expectedEndDate,
    realEndDate: baseContract.realEndDate,
    addendums: baseContract.addendums,
  });
  
  const onSetField = (key, value) => {
    setContractState(current => ({
      ...current,
      [key]: value,
    }));
  }

  const onAddAddendum = () => {
    setContractState(current => {
      return {
        ...current,
        addendums: [
          ...(current.addendums || []),
          {
            id: getRandomString(4),
            startDate: baseContract.startDate,
            endDate: baseContract.endDate,
          }
        ]
      }
    });
  }

  const onAddendumSetField = (addendumId, key, value) => {
    setContractState(current => ({
      ...current,
      addendums:_.map(current.addendums, (addendum) => (
        addendum.id === addendumId ? ({
          ...addendum,
          [key]: value,
        }) : addendum
      ))
    }));
  }

  const onRemoveAddendum = (addendumId) => {
    setContractState(current => ({
      ...current,
      addendums:_.filter(current.addendums, ({ id }) => id !== addendumId)
    }));
  }

  return (
    <div
      style={{
        position: 'relative',
        border: '1px solid lightgrey',
        borderRadius: 8,
        marginBottom: 10,
        padding: 10,
        paddingTop: 0,
      }}
    >
      <h4>{baseContract.id ? 'Modifier le contract' : 'Créer le contract'}</h4>
      <div>
        Titre:<br/>
        <input 
          value={contractState.title}
          onChange={({ target }) => onSetField('title', target.value)}
        />
      </div>
      <br/>
      <div>
        Début:<br/>
        <input 
          value={contractState.startDate}
          onChange={({ target }) => onSetField('startDate', target.value)}
        />
      </div>
      <br/>
      <div>
        Fin:<br/>
        <input 
          value={contractState.endDate}
          onChange={({ target }) => onSetField('endDate', target.value)}
        />
      </div>
      <br/>
      <div>
        Fin prévisionnelle:<br/>
        <input 
          value={contractState.expectedEndDate}
          onChange={({ target }) => onSetField('expectedEndDate', target.value)}
        />
      </div>
      <br/>
      <div>
        Fin réelle:<br/>
        <input 
          value={contractState.realEndDate}
          onChange={({ target }) => onSetField('realEndDate', target.value)}
        />
      </div>
      <br/>
      <div>
        Label SIRET:<br/>
        <input 
          value={contractState.siret}
          onChange={({ target }) => onSetField('siret', target.value)}
        />
      </div>
      <br/>
      <div>
        Label Qualification:<br/>
        <input 
          value={contractState.qualification}
          onChange={({ target }) => onSetField('qualification', target.value)}
        />
      </div>
      <br/>
      <div>
        Avenants:<br/>
        <div style={{ paddingLeft: 10 }}>
          {_.isEmpty(contractState.addendums) ? <i>Aucun avenant<br/></i> : ''}
          {_.map(contractState.addendums, (addendum) => (
            <li key={addendum.id}>
              <input 
                size={10}
                value={addendum.startDate}
                onChange={({ target }) => onAddendumSetField(addendum.id, 'startDate', target.value)}
              />
              {' - '}
              <input 
                size={10}
                value={addendum.endDate}
                onChange={({ target }) => onAddendumSetField(addendum.id, 'endDate', target.value)}
              />
              {' '}
              ({addendum.id})
              <button 
                style={{ marginLeft: 10 }} 
                onClick={() => onRemoveAddendum(addendum.id)}
              >
                -
              </button>
            </li>
          ))}
          <button style={{ marginTop: 5 }} onClick={() => onAddAddendum()}>Ajouter avenant</button>
        </div>
      </div>
      <br/>


      {onLeaveEditionMode ? (
        <button
          onClick={onLeaveEditionMode}
          style={{
            cursor: 'pointer', 
            marginRight: 10 
          }}
        >
          Annuler
        </button>
      ) : ''}

      <button
        onClick={() => onSave({ contract: contractState })}
        style={{ cursor: 'pointer', marginRight: 40 }}
      >
        {contractState.id ? 'Modifier' : 'Créer'}
      </button>

      {contractState.id  ? (
        <button
          onClick={() => onSave({ contract: { ...contractState, toDelete: true }})}
          style={{ 
            position: 'absolute',
            right: 10,
            bottom: 10, 
            cursor: 'pointer', 
            marginRight: 10 
          }}
        >
          Supprimer ce contrat
        </button>
      ) : ''}
    </div>
  );
}

const ContractsEditor = ({ 
  contracts, 
  onSelectContractForDisplay,
  onUnselectContractForDisplay,
  shownContractIds,
  onCreateContract, 
  onUpdateContract,
  onDeleteContract,
  currentDate,
  lastActivationDate,
  series,
}) => {
  const [editionState, setEditionState] = useState({});

  const onSave = ({ contract }) => {
    if (!contract.id) {
      onCreateContract({
        id: getRandomString(6),
        ...contract,
      })
    } else {
      if (contract.toDelete) {
        onDeleteContract({ id: contract.id });
      } else {
        onUpdateContract({
          ...contract
        })  
      }
    }
    setEditionState({});
  }

  const onLeaveEditionMode = () => {
    setEditionState({});
  }

  const contractsWithMainEvents = _.map(contracts, contract => ({
    ...contract,
    mainEvents: getContractMainEvents({ contract, contracts, lastActivationDate })
  }));

  const getColorBySerieIndex = (index) => {
    return [
      'blue','orange', 'green',
      'purple', 'rosa', 'lime',
      'brown', 'teal', 'fuchsia', 'yellow'
    ][index%10];
  }

  const colorByContractId = {};
  _.each(series, (serie, index) => {
    const color = getColorBySerieIndex(index)
    _.each(serie.contracts, ({ id }) => {
      colorByContractId[id] = color;
    })
  })

  return (
    <div>
      {_.isEmpty(contracts) ? (
        <div>
          <i>Pas de contrats renseignés</i>
          <br/><br/>
        </div>
      ) : ''}
      {_.map(contractsWithMainEvents || [], (contract) => (
        <div
          key={contract.id}
        > 
          {editionState.baseContract?.id === contract.id ? (
            <ContractEditor
              key={editionState.editionKey}
              baseContract={editionState.baseContract}
              onSave={onSave}
              onLeaveEditionMode={onLeaveEditionMode}
            />
          ) : (
            <div 
              style={{
                position: 'relative',
                border: '1px solid black',
                borderRadius: 8,
                marginBottom: 10,
                padding: 10,
                paddingTop: 0,
              }}
            >
              <h5 style={{ 
                marginTop: 10, 
                marginBottom: 10,
                color: colorByContractId[contract.id],
              }}>
                {contract.title} ({contract.id})
                {contract.siret ? ` (${contract.siret})` : '' }
                {contract.qualification ? ` (${contract.qualification})` : '' }
              </h5>
              <span>De {contract.startDate} à {contract.endDate}</span>
              {(contract.expectedEndDate ? (
                <div>Fin prévue : {contract.expectedEndDate}</div>
              ) : '')} 
              {(contract.realEndDate ? (
                <div>Fin réelle : {contract.realEndDate}</div>
              ) : '')}
              {false && (window.location?.href || '').indexOf('details') >= 0 && (
                <div style={{ background: 'whitesmoke', paddingLeft: 10 }}>
                  <div>#SmartEnd: {getContractSmartEndDate({ contract, contracts })}</div>
                  <div> $DM1: {_.findWhere(contract.mainEvents?.startEvents, { type: 'start-1'})?.date || 'X'}</div>
                  <div> $DM2: {_.findWhere(contract.mainEvents?.startEvents, { type: 'start-2'})?.date || 'X'}</div>
                  <div> $FM1: {_.findWhere(contract.mainEvents?.endEvents, { type: 'end-1'})?.date || 'X'}</div>
                  <div> $FM2: {_.findWhere(contract.mainEvents?.endEvents, { type: 'end-2'})?.date || 'X'}</div>
                  <br/>
                </div>
              )}
              
              {_.map(contract.addendums, (addendum, index) => (
                <div key={addendum.id} style={{ paddingLeft: 20, fontStyle: 'italic' }}>
                  Avenant {index+1} : {addendum.startDate} - {addendum.endDate} ({addendum.id})
                </div>  
              ))}
              <button
                style={{
                  position: 'absolute',
                  top: 8,
                  right: 10,
                  cursor: 'pointer'
                }} 
                onClick={() => setEditionState({
                  editionKey: getRandomString(12),
                  baseContract: contract,
                })}
              >
                Modifier
              </button>
              <div 
                style={{
                  position: 'absolute',
                  top: 32,
                  right: 10,
                  cursor: 'pointer',
                  height: 10,
                  width: 10,
                  borderRadius: 5,
                  border: '1px solid lightgrey', 
                  backgroundColor: shownContractIds.includes(contract.id) ? 'yellow' : 'white'
                }}
                onClick={() => {
                  if (!shownContractIds.includes(contract.id)) {
                    onSelectContractForDisplay({ contractId: contract.id });
                  } else {
                    onUnselectContractForDisplay({ contractId: contract.id });
                  }
                }}
              >  
              </div>
            </div>
          )}
        </div>
      ))}
      {(editionState.editionKey && !editionState.baseContract?.id) ? (
        <ContractEditor
          key={editionState.editionKey}
          baseContract={editionState.baseContract}
          onSave={onSave}
          onLeaveEditionMode={onLeaveEditionMode}
        />
      ) : ''}
      <div>
        {!(editionState.editionKey && !editionState.baseContract?.id) ? (
          <button 
            style={{ cursor: 'pointer' }}
            onClick={() => setEditionState({
              editionKey: getRandomString(12),
              baseContract: { 
                title: `Contract ${getRandomString(3)}`, 
                qualification: '',
                siret: '',
                startDate: currentDate,
                realEndDate: '',
                expectedEndDate: '',
                endDate: new Date(
                  new Date(currentDate).getTime() + 7*NB_MS_IN_A_DAY
                ).toISOString().slice(0,10)
              }
            })}
          >
            Ajouter un contrat
          </button>
        ) : ''}
      </div>
    </div>
  )
}

export default ContractsEditor;