import _ from 'underscore';
import { CandidateState, Event, Serie } from "../../../types.ts";
import getEventsFromSerie from "./getKeyEventsFromSeries.ts";
import { getRandomString } from '../../../common.js';
import getInterseriesFromSeriesAndStartDate from '../../../helpers/interseries.ts';
import getEventsFromInterserie from './getKeyEventsFromInterserie.ts';

const getKeyEvents = ({ 
  currentDate,
  candidateState,
} : { 
  currentDate: string; 
  candidateState: CandidateState;
}) : Event[] => {

  const { lastActivationDate, lastTouchpointDate } = candidateState; 

  if (!lastActivationDate) {
    return [];
  }

  const keyEvents : (Event & { serieIndex?: number })[] = [];

  const series = candidateState.series ?? [];

  // Series events
  let serieIndex: number = 0;
  const serieByIndex : Record<number, Serie> = {};
  for (const serie of series) {
    const currentSerieIndex = serieIndex;
    serieByIndex[currentSerieIndex] = serie;
    const serieEvents = getEventsFromSerie({ currentDate, candidateState, serie });
    const serieEventsWithIndex = _.map(
      serieEvents, 
      (serieEvent) => ({ ...serieEvent, serieIndex: currentSerieIndex })
    );
    keyEvents.push(...serieEventsWithIndex);
    serieIndex += 1;
  }

  const interseries = getInterseriesFromSeriesAndStartDate({ 
    series, 
    startDate: lastActivationDate 
  });

  const allInterserieEvents : Event[] = [];
  for (const interserie of interseries) {
    if (lastTouchpointDate && lastTouchpointDate >= interserie.endDate) {
      continue;
    }
    let maxLeftEventDate : string = lastActivationDate;
    let minRightEventDate : string | undefined = undefined;
    if (lastTouchpointDate && lastTouchpointDate > maxLeftEventDate) {
      maxLeftEventDate = lastTouchpointDate;
    }
    for (const event of keyEvents) {
      const serie = event.serieIndex !== undefined ? serieByIndex[event.serieIndex] : null;
      const side : 'left' | 'right' | undefined = (
        serie ? (
          serie.endDate <= interserie.startDate ? 'left' : 'right'
        ) : event.date <= interserie.startDate ? (
          'left'
        ) : event.date >= interserie.endDate ? (
          'right'
        ) : undefined
      );
      if (side === 'left') {
        if (event.date >= maxLeftEventDate) {
          maxLeftEventDate = event.date;
        }
      }
      if (side === 'right') {
        if (!minRightEventDate || event.date < minRightEventDate) {
          minRightEventDate = event.date;
        }
      }
    }
    const interserieEvents = getEventsFromInterserie({
      currentDate,
      candidateState,
      interserie,
      maxLeftEventDate,
      minRightEventDate,
    });
    allInterserieEvents.push(...interserieEvents);
  }

  keyEvents.push(...allInterserieEvents);

  const sortedKeyEvents = _.map(
    _.sortBy(
      _.sortBy(keyEvents, ({ type }) => getTypePriority({ type })), 
      'date'
    ),
    (ev) => ({ ...ev, id: getRandomString(8) })
  );
 

  const eventByDate : Record<string, Event> = {};
  for (const event of sortedKeyEvents) {
    const refDate = event.date < currentDate ? currentDate : event.date;
    eventByDate[refDate] = event;
  }

  return _.sortBy(_.values(eventByDate), 'date');
}

const getTypePriority = ({ type } : { type: string }) => {
  if ((type ?? '').includes('start')) {
    return 3;
  }
  if ((type ?? '').includes('end')) {
    return 2;
  }
  if (type === 'followup') {
    return 1;
  }
  return 0;
}

export default getKeyEvents;