import _ from 'underscore';
import { 
  getDayAfterBusinessOffset, 
  getDayAfterOffset, 
  getDayAfterTransformPolicy, 
  getDiffDatesInDays, 
  getFirstBusinessDayEqualOrAfter 
} from "../../common";
import metaParameters from "../metaParameters"
import { getContractPeriodLength, getContractSmartEndDate } from "./helpers"


const getContractStartEventDate = ({ contract, eventId, contracts }) => {
  const refPeriodLength = getContractPeriodLength(
    contract, 
    metaParameters.contractStartEvents.referencePeriodForEventShifts
  );

  const modulation = getStartEventModulation({ contract, contracts });

  const mode = (
    refPeriodLength >= metaParameters.minNbDaysForBeingLongPeriod ? 'long' : 'short'
  ) + ':' + (
    modulation
  );

  const { 
    isDisabled, 
    offsetFromStart 
  } = metaParameters.contractStartEvents[eventId].behaviourByMode[mode];
  
  if (isDisabled) {
    return null;
  }

  const preResult = getFirstBusinessDayEqualOrAfter(
    getDayAfterTransformPolicy(contract.startDate, offsetFromStart)
  );
  
  const smartEndDate = getContractSmartEndDate({ contract });
  
  return preResult <= smartEndDate ? preResult : null;
}

export const getStartEventModulation = ({ contract, contracts }) => {
  const cliffDate = getDayAfterOffset(
    contract.startDate, 
    -metaParameters.maxNbDaysForSimilarContractsCheck
  );
  let modulation = 'classic';
  if (contract.siret) {
    for (const otherContract of contracts) {
      if (otherContract.siret !== contract.siret) {
        continue;
      }
      if (
        otherContract.startDate < contract.startDate && 
        getContractSmartEndDate({ contract: otherContract }) >= cliffDate
      ) {
        modulation = 'disabled'; // #LAURENT: new rule of 23-10-20
        /*
        if (modulation === 'classic') {
          modulation = 'delayed';
        }
        if (contract.qualification && contract.qualification === otherContract.qualification) {
          modulation = 'disabled';
        } 
        */
      }
    }
  }
  return modulation;
}

export const getContractFirstStartEventDate = ({ contract, contracts }) => {
  return getContractStartEventDate({ contract, eventId: 'start-1', contracts })
}

export const getContractSecondStartEventDate = ({ contract, contracts }) => {
  return getContractStartEventDate({ contract, eventId: 'start-2', contracts })
}

export const getStartEventsFromContract = ({ contract, contracts }) => {

  if (contract.isFirstEndOk || contract.isSecondEndOk) {
    console.log('short cut')
    return [];
  }

  const firstStartDate = getContractFirstStartEventDate({ contract, contracts });
  const secondStartDate = getContractSecondStartEventDate({ contract, contracts });

  const lastStartEventsCompletionDates = [
    ..._.compact([contract.firstStartCompletionDate]),
    ..._.compact([contract.secondStartCompletionDate]),
  ].sort();

  const lastStartCompletionDate = !_.isEmpty(lastStartEventsCompletionDates) ? (
    _.last(lastStartEventsCompletionDates)
  ) : null;

  const minimalDate = lastStartCompletionDate ? (
    getDayAfterBusinessOffset(lastStartCompletionDate, 2)
  ) : null;

  //console.log('minDate');

  const firstStartEvent = firstStartDate && (!minimalDate || firstStartDate >= minimalDate) ? {
    contractId: contract.id,
    contractTitle: contract.title,
    type: 'start-1',
    modulation: getStartEventModulation({ contract, contracts }),
    date: firstStartDate,
    maxReferenceDueDate: firstStartDate,
  } : null;

  const secondStartEvent = secondStartDate && (!minimalDate || secondStartDate >= minimalDate) ? {
    contractId: contract.id,
    contractTitle: contract.title,
    type: 'start-2',
    date: secondStartDate,
    maxReferenceDueDate: secondStartDate,
  } : null;

  if (firstStartEvent && secondStartDate && firstStartEvent.date === secondStartDate.date) {
    return [firstStartEvent];
  }

  return [
    ..._.compact([firstStartEvent]),
    ..._.compact([secondStartEvent]),
  ];

}
