import _ from 'underscore';
import getTimeTriggerUpdatedCFD from './timeTriggerUpdatedCFD.ts';
import v2UpdatedCFD from './v2UpdatedCFD.ts';
import getSmartlyUpdatedCFDWhenNoProjects from './noProjectUpdatedCFD.ts';
import { CandidateState, ContactFlowDescriptor, ContactFlowSimulatorSystem, EmittedEvent } from '../types.ts';

const getSmartlyUpdatedCFDAndEmittedEvents = ({ 
  contactFlowSimulatorSystem,
  contactFlowDescriptor,
  candidateState,
} : {
  contactFlowSimulatorSystem: ContactFlowSimulatorSystem;
  contactFlowDescriptor: ContactFlowDescriptor;
  candidateState: CandidateState ;
}) : { 
  updatedContactFlowDescriptor: ContactFlowDescriptor,
  emittedEvents: EmittedEvent[];
} => {

  const getTimeUpdated = (descriptor : ContactFlowDescriptor) => getTimeTriggerUpdatedCFD({
    contactFlowSimulatorSystem,
    contactFlowDescriptor: descriptor,
  });

  const baseDescriptor = getTimeUpdated(contactFlowDescriptor);
  const priority = getPriority({ candidateState });
  
  if (priority === 'sleeping') {
    const updatedContactFlowDescriptor = getTimeUpdated(
      getSmartlyUpdatedCFDWhenNoProjects({
        contactFlowSimulatorSystem,
        contactFlowDescriptor: baseDescriptor,
      }),
    ); 
    return {
      updatedContactFlowDescriptor,
      emittedEvents: [],
    }
  }

  const { updatedContactFlowDescriptor, emittedEvents } = v2UpdatedCFD({
    contactFlowSimulatorSystem,
    contactFlowDescriptor: baseDescriptor,
    candidateState,
    priority,
  });

  return {
    updatedContactFlowDescriptor: getTimeUpdated(updatedContactFlowDescriptor),
    emittedEvents,
  }
}


const getPriority = ({ candidateState } : { candidateState : CandidateState }) => (
  _.findWhere(candidateState.projects, { type: 'high-priority' }) ? (
    'high-priority'
  ) : _.findWhere(candidateState.projects, { type: 'low-priority' }) ? (
    'low-priority'
  ) : 'sleeping'
);


export default getSmartlyUpdatedCFDAndEmittedEvents;