import { getDayAfterOffset, getMondayOfPreviousWeek } from "../../common";
import { getContractSmartEndDate } from "./helpers";

export const isContractCollectable = ({ contract, lastActivationDate, currentDate }) => {
  const mondayOfPreviousWeek = getMondayOfPreviousWeek(currentDate);
  const smartEnd = getContractSmartEndDate({ contract });
  if (contract.lastTouchpointDate) {
    return false;
  }
  return contract.creationDate > lastActivationDate && smartEnd < currentDate && smartEnd >= mondayOfPreviousWeek;
}

export const getContractCollectEventDate = ({ contract , currentDate }) => {
  if (!contract.isCollectable) {
    return null;
  }
  if (contract.lastTouchpointDate) {
    return null;
  }
  const previousMonday = getMondayOfPreviousWeek(currentDate);
  const curMonday = getDayAfterOffset(previousMonday, 7);
  const smartEnd = getContractSmartEndDate({ contract });
  return smartEnd >= previousMonday && smartEnd < curMonday ? (
    curMonday
  ) : null;
}
