import _ from 'underscore';
import { getRandomString } from '../../common';
import React, { useState } from 'react';



const TraceManager = ({ trace, onLoadPlayableTrace }) => {

  const [mode, setMode] = useState('');
  const [identifier, setIdentifier] = useState('');
  const [traceDescriptor, setTraceDescriptor] = useState('[]');
  const [traceDescriptorIsOk, setTraceDescriptorIsOk] = useState(true);
  const [loadingMode, setLoadingMode] = useState('edition');


  return (
    <div>
      {mode === 'saving' ? (
        <div
          style={{
            position: 'relative',
            border: '1px solid lightgrey',
            borderRadius: 8,
            marginBottom: 10,
            padding: 10,
            paddingTop: 0,
          }}
        >
          <h4>Sauvegarder la trace</h4>
          <div>
            Identifiant :<br/>
            <input 
              value={identifier}
              onChange={({ target }) => setIdentifier(target.value)}
            />
            <br/><br/>
          </div>
          <button
            style={{ cursor: 'pointer', marginRight: 10 }} 
            onClick={() => setMode('')}
          >Annuler</button>
          <button
            style={{ cursor: 'pointer', marginLeft: 10 }} 
            onClick={() => {
              if (!identifier) {
                return alert('Identifiant vide')
              }
              setMode('');
              localStorage.setItem(
                'trace__' + identifier, 
                JSON.stringify(
                  _.map(
                    trace, 
                    (traceItem, index) => _.omit({ stepIndex: index, ...traceItem }, 'state'),
                  ), null, 4
                )
              );
            }}
          >Sauvegarder</button>
        </div>
      ) : mode === 'loading' ? (
        <div
          style={{
            position: 'relative',
            border: '1px solid lightgrey',
            borderRadius: 8,
            marginBottom: 10,
            padding: 10,
            paddingTop: 0,
          }}
        >
          <h4>Charger une trace</h4>
          <select value={loadingMode} onChange={
            (e) => {
              setTraceDescriptor('[]');
              setTraceDescriptorIsOk(true);
              setLoadingMode(e.target.value);
            }}>
            <option value={'edition'}>JSON</option>
            <option value={'selection'}>Traces sauvegardées</option>
          </select>
          {loadingMode === 'selection' ? (
            <div>
              <br/>
              {_.compact(_.map(_.keys(localStorage), (key) => (
                key.indexOf('trace_') === 0 ? (
                  <li key={key} style={{ marginBottom: 4 }}>
                    <button 
                      style={{ cursor: 'pointer' }}
                      onClick={() => {
                        const item = localStorage.getItem(key);
                        console.log(item);
                        try {
                          const parsed = JSON.parse(item);
                          onLoadPlayableTrace({ 
                            playableTrace: parsed 
                          });
                          setMode('');
                        } catch (e) {
                          alert('invalid JSON');
                        }
                      }}
                    >
                      {key.slice(7)}
                    </button>
                  </li>
                ) : null
              )))}
              <br/>
            </div>
          ) : (
            <div>
              <textarea 
                rows={30}
                cols={40}
                style={{
                  border: traceDescriptorIsOk ? '1px solid lime' : '1px solid red',
                  outline: 'none'
                }}
                value={traceDescriptor}
                onChange={(e) => {
                  setTraceDescriptor(e.target.value);
                  try {
                    JSON.parse(e.target.value);
                    setTraceDescriptorIsOk(true);
                  } catch(e) {
                    setTraceDescriptorIsOk(false);
                  }
                }}
              />
              <br/>
            </div>
          )}
          <button
            style={{ cursor: 'pointer', marginRight: 10  }} 
            onClick={() => setMode('')}
          >Annuler</button>
          <button
            style={{ cursor: 'pointer' }} 
            onClick={() => {
              try {
                const parsed = JSON.parse(traceDescriptor);
                onLoadPlayableTrace({ 
                  playableTrace: parsed 
                });
                setMode('');
              } catch (e) {
                alert('invalid JSON');
              }
            }}
          >Charger
          </button>
        </div>
      ) : (
        <div>
          <button
            style={{ cursor: 'pointer' }} 
            onClick={() => { 
              setIdentifier(''); 
              setMode('saving') 
            }}
          >
            Sauvegarder la trace
          </button>
          <button
            style={{ marginLeft: 10, cursor: 'pointer' }}
            onClick={() => {
              setTraceDescriptor('[]');
              setTraceDescriptorIsOk(true);
              setLoadingMode('edition'); 
              setMode('loading') 
            }}
          >
            Charger une trace
          </button>
        </div>
      )}
      <br/>
      <pre>
        {JSON.stringify(
          _.map(
            trace, 
            (traceItem, index) => _.omit({ stepIndex: index, ...traceItem }, 'state'),
          ), null, 4
        )}
      </pre>
    </div>
  );
}

export default TraceManager;