import _ from 'underscore';
import { getRandomString } from "../../../common";
import { Achievement, Action, Event } from "../../../types";

export const titleFromEventType = {
  'start-1': 'Premier appel mission',
  'start-2': 'Deuxième appel mission',
  'followup': 'Suivi de mission',
  'intermission-followup': "Suivi d'intermission",
  'end-1': 'Pré fin de mission',
  'end-2': 'fin de mission',
  'payment-point': 'Point paie',
  'collect': 'Ramasse-miettes',
};

export const getActionFromEvent = ({ event } : { event : Event }) : Action => {
  return {
    id : `contact-flow-action-${getRandomString(8)}`,
    type : "manual",
    trigger : {
      type: 'after-date',
      date: new Date(event.date).toISOString(),
    },
    title : {
      default : {
        'start-1': 'Premier appel mission',
        'start-2': 'Deuxième appel mission',
        'followup': 'Suivi de mission',
        'intermission-followup': "Suivi d'intermission",
        'end-1': 'Pré fin de mission',
        'end-2': 'fin de mission',
        'payment-point': 'Point paie',
        'collect': 'Ramasse-miettes',
      }[event.type] || 'Action - autre' ,
    },
    description : {
      default : getDescription({ event }),
    },
    metadata: {
      achievements: getAchievements({ event }),
      unitEvents: [event, ...(event.subEvents || [])],
    },
    ...event.maxReferenceDueDate && {
      referenceDueDate: event.maxReferenceDueDate,
    },
  };
}


export const getDescription = ({ event } : { event : Event }) : string => {

  const mainDescription = getUnitEventDescription({ event });
  const secondaryDescriptions = _.map(event.subEvents ?? [], subEvent => (
    getUnitEventDescription({ event: subEvent })
  ));

  if (_.isEmpty(secondaryDescriptions)) {
    return mainDescription;
  } else {
    return _.map([mainDescription, ...secondaryDescriptions], description => (
      `• ${description}`
    )).join('\n');
  }
}

export const getUnitEventDescription = ({ event } : { event: Event }) : string => {
  let result = titleFromEventType[event.type] || 'Inconnu';
  if (event.contractId) {
    result += ` (${event.contractId})`;
  }
  if (event.contractIds && !_.isEmpty(event.contractIds)) {
    result += ` (${event.contractIds.join(', ')})`;
  }
  if (event.nbSuccessiveNonReplies && event.nbSuccessiveNonReplies % 3 !== 0) {
    result += ` [${event.nbSuccessiveNonReplies % 3}e relance]`
  }

  return result;
}

export const getUnitEventAchievements = ({ event } : { event : Event }) : Achievement[] => (
  [
    {
      type: event.type,
      ...event.siret && {
        siret: event.siret,
      },
      ...event.followupIndex && {
        followupIndex: event.followupIndex,
      },
      ...event.contractId && {
        contractId: event.contractId,
      },
    }
  ]
);

export const getAchievements = ({ event } : { event : Event }) : Achievement[] => (
  _.flatten(_.map(
    [event, ...(event.subEvents || [])],
    (unitEvent) => getUnitEventAchievements({ event: unitEvent })
  ))  
);