import React, { useState } from 'react';

const CommentEditor = ({ addComment }) => {
  const [state, setState] = useState({});

  const onSetField = (key, value) => {
    setState(state => ({
      ...state,
      [key]: value,
    }));
  }

  return (
    <div>
      {!state.isEditing ? (
        <button 
          style={{ 
            cursor: 'pointer'
          }}
          onClick={() => setState({
            isEditing: true,
            comment: '', 
          })}
        >
          Commencer
        </button>
      ) : (
        
        <div
          style={{
            position: 'relative',
            border: '1px solid lightgrey',
            borderRadius: 8,
            marginBottom: 10,
            padding: 10,
            paddingTop: 0,
          }}
        >
          <h4>{"Enregistrer le commentaire"}</h4>
          <div>
            Commentaire :<br/>
            <input 
              value={state.comment}
              onChange={({ target }) => onSetField('comment', target.value)}
            />
          </div>
          <br/>
          <button
            style={{ cursor: 'pointer' }} 
            onClick={() => setState({})}
          >
            Annuler
          </button>
          <button
            style={{ cursor: 'pointer', marginLeft: 10 }} 
            onClick={() => {
              setState({});
              addComment({ 
                comment: state.comment,
              });
            }}
          >Enregister</button>
        </div>
      )}
    </div>
  )
}

export default CommentEditor;