import _ from 'underscore';
import { CandidateState, EmittedEvent, SiretCandidateState } from "../../types"

export const mergeCandidateSiretStates = (
  siretStates1 : Record<string,SiretCandidateState>, 
  siretStates2 : Record<string,SiretCandidateState>
) : Record<string,SiretCandidateState> => {
  return {
    ...siretStates2,
    ..._.mapObject(siretStates1, (siretState1, siret) => ({
      ...siretState1,
      ...siretStates2[siret]
    }))
  };
}

export const getCandidateStateAfterReduction = ({ 
  candidateState, 
  emittedEvents 
} : {
  candidateState : CandidateState;
  emittedEvents : EmittedEvent[];
}) : CandidateState => {
  return _.reduce(emittedEvents, reduceOnSingleEvent, candidateState);
}

const reduceOnSingleEvent = (
  candidateState: CandidateState, 
  event: EmittedEvent,
) : CandidateState => {
  if (event.type === 'new-rolling-task') {
    const { droppedAction, newAction } = event;
    if (droppedAction) {
      console.log('DROP', droppedAction);
    }
    return {
      ...candidateState,
      rollingTask: newAction,
    }    
  }
  return candidateState;
}

