import _ from 'underscore';
import React, { useState } from 'react';

const assetionTypeOptions = [
  { id: 'next-action', text: 'Prochaine action' },
  { id: 'next-action-main', text: 'Prochaine action - Principale' },
  { id: 'next-action-secondaries', text: 'Prochaine action - Secondaires' },
  { id: 'any-action', text: 'Action quelconque' },
  { id: 'any-action-secondaries', text: 'Action quelconque - Secondaires' },
  { id: 'serie-span', text: 'Étendue série' },
  { id: 'reference-date-dm', text: 'Date de référence - DM' },
  { id: 'reference-date-fm', text: 'Date de référence - FM' },
  { id: 'reference-date-pi', text: 'Date de référence - PI' },
  { id: 'reference-date-a', text: 'Date de référence - A' },
  /*
  { id: 'reference-date-dm1', text: 'Date de référence - DM1' },
  { id: 'reference-date-dm2', text: 'Date de référence - DM2' },
  { id: 'reference-date-fm1', text: 'Date de référence - FM1' },
  { id: 'reference-date-fm2', text: 'Date de référence - FM2' },
   */
];

const actionCategories = [
  { id: 'unconstraint', text: 'Non contraint' },
  { id: 'intermission', text: 'Intermission' },
  { id: 'start-mission', text: 'Début mission' },
  { id: 'end-mission', text: 'Fin mission' },
  { id: 'followup-mission', text: 'Suivi mission' },
  { id: 'start-mission-pi', text: 'PI' },
  { id: 'start-mission-a', text: 'A' },
  { id: 'start-mission-pia', text: 'PIA' },
  /*
  { id: 'start-mission-1', text: 'Début mission 1' },
  { id: 'start-mission-2', text: 'Début mission 2' },
  { id: 'end-mission-1', text: 'Fin mission 1' },
  { id: 'end-mission-2', text: 'Fin mission 2' },
  { id: 'payment', text: 'Paie' },
  { id: 'collect', text: 'Ramasse-miettes' },
   */
];

const existenceOptions = [
  { id: 'unconstraint', text: 'Non contraint' },
  { id: 'should-exists', text: 'Oui' },
  { id: 'should-not-exists', text: 'Non' },
]


const AssertionEditor = ({ addAssertion }) => {
  const [state, setState] = useState({});

  const onSetField = (key, value) => {
    setState(state => ({
      ...state,
      [key]: value,
    }));
  }

  return (
    <div>
      {!state.isEditing ? (
        <button 
          style={{ 
            cursor: 'pointer'
          }}
          onClick={() => setState({
            isEditing: true,
            title: '',
            comment: '', 
            assertionType: 'next-action',
            minDate: '',
            maxDate: '',
            contractId: '',
            existence: 'unconstraint',
            actionCategory: 'unconstraint',
          })}
        >
          Commencer
        </button>
      ) : (
        
        <div
          style={{
            position: 'relative',
            border: '1px solid lightgrey',
            borderRadius: 8,
            marginBottom: 10,
            padding: 10,
            paddingTop: 0,
          }}
        >
          <h4>{"Enregistrer le checkpoint"}</h4>

          <div>
            Type :
            <br/>
            <select 
              value={state.assertionType}
              onChange={e => {
                onSetField('assertionType', e.target.value)
              }}
            >
              {_.map(assetionTypeOptions,({ id, text }) => (
                <option key={id} value={id}>{text}</option>
              ))}
            </select>
          </div>
          <br/>
          <div>
            Titre :<br/>
            <input 
              value={state.title}
              onChange={({ target }) => onSetField('title', target.value)}
            />
          </div>
          <br/>
          <div>
            Commentaire :<br/>
            <input 
              value={state.comment}
              onChange={({ target }) => onSetField('comment', target.value)}
            />
          </div>
          <br/>
          {(
            (state.assertionType || '').indexOf('reference-date') >= 0 ||
            state.assertionType === 'serie-span'
          ) ? (
            <div>
              <div>
                Contrat (id)*:<br/>
                <input 
                  value={state.contractId}
                  onChange={({ target }) => onSetField('contractId', target.value)}
                />
              </div>
              <br/>
            </div>
          ) : ''}
          
          {state.assertionType !== 'serie-span' ? (
            <div>
              Existence :
              <br/>
              <select 
                value={state.extistence}
                onChange={e => onSetField('existence', e.target.value)}
              >
                {_.map(existenceOptions,({ id, text }) => (
                  <option key={id} value={id}>{text}</option>
                ))}
              </select>
              <br/>
              <br/>
            </div>
          ) : ''}

          <div>
            Date min :<br/>
            <input 
              value={state.minDate}
              onChange={({ target }) => onSetField('minDate', target.value)}
            />
          </div>
          <br/>
          <div>
            Date max :<br/>
            <input 
              value={state.maxDate}
              onChange={({ target }) => onSetField('maxDate', target.value)}
            />
          </div>
          <br/>
          {(
            (state.assertionType || '').indexOf('next-action') >= 0 ||
            (state.assertionType || '').indexOf('any-action') >= 0
          ) ? (
            <div>
              <div>
                Contrat (id):<br/>
                <input 
                  value={state.contractId}
                  onChange={({ target }) => onSetField('contractId', target.value)}
                />
              </div>
              <br/>
            </div>
          ) : ''}
          {(
            (state.assertionType || '').indexOf('next-action') >= 0 ||
            (state.assertionType || '').indexOf('any-action') >= 0
          ) ? (
            <div>
              <div>
                Type d'action:
                <br/>
                <select 
                  value={state.actionCategory}
                  onChange={e => onSetField('actionCategory', e.target.value)}
                >
                  {_.map(actionCategories,({ id, text }) => (
                    <option key={id} value={id}>{text}</option>
                  ))}
                </select>
              </div>
              <br/>
            </div>
          ) : ''}
          
          <button
            style={{ cursor: 'pointer' }} 
            onClick={() => setState({})}
          >
            Annuler
          </button>
          <button
            style={{ cursor: 'pointer', marginLeft: 10 }} 
            onClick={() => {
              setState({});
              addAssertion({ 
                assertionType: state.assertionType,
                title: state.title,
                comment: state.comment,
                minDate: state.minDate,
                maxDate: state.maxDate,
                existence: state.existence,
                contractId: state.contractId,
                actionCategory: state.actionCategory,
              });
            }}
          >Enregister</button>
        </div>
      )}
    </div>
  )
}

export default AssertionEditor;