import _ from 'underscore';
import React, { useState, useEffect } from 'react';

const CurrentSequence = ({ 
  contactFlowSimulatorSystem,
  contactFlowDescriptor, 
  onExecuteAction, 
  currentDate,
}) => {

  const [executableActionId, setExecutableActionId] = useState('');

  const currentSequenceDescriptor = _.find(
    contactFlowDescriptor.sequences, 
    ({ isCurrent, completion }) => isCurrent && !completion?.isCompleted
  );


  useEffect(() => {
    const contactFlow = contactFlowSimulatorSystem.createContactFlow(contactFlowDescriptor);
    const currentSequence = contactFlow.getCurrentSequence();

    const update = async () => {
      let newExecutableActionId = '';
      let lastActionIsCompleted = true;
      for (const actionDescriptor of currentSequence.getDescriptor()?.actions || []) {
        const action = currentSequence.getAction({ id: actionDescriptor.id });        
        if (
          !action.getIsCompleted() && 
          lastActionIsCompleted && 
          (
            actionDescriptor.trigger?.type === 'manual-trigger' ||
            await action.getTrigger().accept({ context: {} })
          )
        ) {
          if (!newExecutableActionId) {
            newExecutableActionId = action.getId();
          }
        }
        lastActionIsCompleted = action.getIsCompleted();
      }
      if (newExecutableActionId !== executableActionId) {
        setExecutableActionId(newExecutableActionId);
      }
    }
    update();
  }, [
    contactFlowSimulatorSystem,
    contactFlowDescriptor,
    currentSequenceDescriptor, 
    executableActionId,
    currentDate
  ]);


  return (
    <div>
      {_.isEmpty(currentSequenceDescriptor?.actions) ? (
        <div>
          <i>Aucune action prévue</i>
          <br/><br/>
        </div>
      ) : ''}
      {_.map(currentSequenceDescriptor?.actions || [], (action, actionIndex) => (
        <div 
          key={`action_${actionIndex}`}
          style={{
            border: action.id && action.id === executableActionId  ? (
              '3px solid black'
            ) : '1px solid black',
            borderRadius: 8,
            marginBottom: 10,
            padding: 10,
          }}
        >
          <div>Title: {action.title?.default || ''}{' '}
            <span title={action.referenceDueDate || ''}>
              ({new Date(
                (action.trigger?.date && action.trigger.date > currentDate) ? 
                action.trigger.date : 
                currentDate).toISOString().slice(0,10) || ''
              })
            </span>
            </div>
          <div>Descr:{' '}
            {(action.description?.default || '').indexOf('•') >= 0 ? (
              <pre>
                {action.description.default}
              </pre>
            ) : (
              action.description?.default || ''
            )}
          </div>
          <div>State: {action.completion?.isCompleted ? 'Completed' : 'Pending'}</div>
          {action.id && action.id === executableActionId ? (
            <div>
              <button 
                onClick={() => {
                  onExecuteAction({ actionId: executableActionId, type: 'discussion' })
                }}
                style={{ cursor: 'pointer' }}
              >
                Appel avec discussion
              </button>
              <button 
                onClick={() => {
                  onExecuteAction({ actionId: executableActionId, type: 'to-profile' })
                }}
                style={{ cursor: 'pointer', marginLeft: 10 }}
              >
                Appel sans réponse
              </button>
            </div>
          ) : null}
        </div>
      ))}
    </div>
  );
}

export default CurrentSequence;