import _ from 'underscore';
import { getRandomString } from "../common";
import { Action, ContactFlowDescriptor, ContactFlowSimulatorSystem } from "../types";

const getUpdatedCFDWithNewActions = ({
  contactFlowSimulatorSystem,
  contactFlowDescriptor,
  priority,
  upcomingActions,
} : {
  contactFlowSimulatorSystem: ContactFlowSimulatorSystem;
  contactFlowDescriptor : ContactFlowDescriptor ;
  priority?: string;
  upcomingActions : Action[];
}) => {


  const descriptor = getPreparedDescriptor({
    contactFlowSimulatorSystem,
    contactFlowDescriptor,
    priority,
  });

  const currentSequence = _.findWhere(descriptor.sequences, { isCurrent: true, });
  if (!currentSequence) {
    throw Error('no current sequence found');
  }

  const newActions: unknown[] = [];
  for (const action of currentSequence.actions) {
    if (action.completion?.isCompleted) {
      newActions.push(action);
    } 
  }
  for (const upcomingAction of upcomingActions) {
    newActions.push(upcomingAction)
  }

  const newDescriptor = {
    ...descriptor,
    sequences: _.map(descriptor.sequences, (seq) => (
      seq.id === currentSequence.id ? ({
        ...seq,
        actions: newActions
      }) :  seq
    ))
  }

  const contactFlow = contactFlowSimulatorSystem.createContactFlow(newDescriptor);
 
  return contactFlow.getDescriptor();
}


const getPreparedDescriptor = ({
  contactFlowSimulatorSystem,
  contactFlowDescriptor,
  priority,
}) => {

  const targetLinkedSequenceId = 'within-missions-sequence';

  const livingSequence = _.find(
    contactFlowDescriptor.sequences || [],
    ({ isCurrent, completion }) => isCurrent && !completion?.isCompleted
  );


  const bloomingSequence = {
    id: `within-missions-sequence-${getRandomString(12)}`,
    linkedSequenceId: targetLinkedSequenceId,
    position : "first-contact",
    isCurrent : true,
    actions: [
      {
        "id" : `contact-flow-action-${getRandomString(8)}`,
        "type" : "manual",
        "trigger" : {
          "type" : "manual-trigger"
        },
        "title" : {
          "default" : "En Mission",
        },
        "description" : {
          "default" : "Super, tu es en mission !",
        },
      }
    ]
  };

  if (!livingSequence) {
    //console.log('NO LIVING');
    return {
      ...contactFlowDescriptor,
      sequences: [
        ..._.map(
          (contactFlowDescriptor.sequences || []),
          seq => ({ ...seq, isCurrent:false }),
        ),
        bloomingSequence,
      ]
    }
  } 
  if (livingSequence.linkedSequenceId === targetLinkedSequenceId) {
    //console.log('SAME TARGET');
    return {
      ...contactFlowDescriptor
    };
  }

  const contactFlow = contactFlowSimulatorSystem.createContactFlow(contactFlowDescriptor);
  const newContactFlow = contactFlow.getResultingContactFlowAfterEvent({ 
    event: {
      type: `switch-to-within-mission-${priority}-animation`,
      date: new Date(contactFlowSimulatorSystem.customDateSystem.now()).toISOString(),
    }
  });
  const newCFD = newContactFlow.getDescriptor();
  //console.log('NEW CFD', newCFD);
  return {
    ...newCFD,
    sequences: [
      ..._.map(
        (newCFD.sequences  || []),
        seq => ({ ...seq, isCurrent:false }),
      ),
      bloomingSequence,
    ]
  }
  
}

export default getUpdatedCFDWithNewActions;