import _ from 'underscore';
import { getDayAfterBusinessOffset } from "../../common";
import { getEndEventsFromContract } from "./contractEndEvents";
import { getStartEventsFromContract } from "./contractStartEvents";


const getContractMainEvents = ({ contract, contracts, lastActivationDate }) => {

  let startEvents = getStartEventsFromContract({ contract, contracts });
  let endEvents = getEndEventsFromContract({ contract, contracts });

  while (                                                                                                                                                                                                          
    !_.isEmpty(startEvents) && !_.isEmpty(endEvents) &&                                                                                                                                                            
    getDayAfterBusinessOffset(_.last(startEvents).date, 1) >=  _.first(endEvents).date                                                                                                                                                                                   
  ) {                                                                             
    startEvents.pop();
  }  

  startEvents = _.filter(startEvents, (event) => 
    (!lastActivationDate || event.date >= lastActivationDate) && 
    (!contract.creationDate || event.date >= contract.creationDate.slice(0,10))
  );
  endEvents = _.filter(endEvents, (event) => 
    (!lastActivationDate || event.date >= lastActivationDate) && 
    (!contract.creationDate || event.date >= contract.creationDate.slice(0,10))
  );

  return {
    startEvents,
    endEvents,
  }
}

export default getContractMainEvents;
