import _ from 'underscore';
import React, { useState } from 'react';

const activityOptions = [
  { id: 'discussion', text: 'Discussion' },
  { id: 'from-profile', text: 'Message reçu' },
  { id: 'to-profile', text: 'Message/Appel émis' },  
];

const ActivityEditor = ({ currentDate, addToTimeline }) => {
  const [state, setState] = useState({});

  const onSetField = (key, value) => {
    setState(state => ({
      ...state,
      [key]: value,
    }));
  }

  return (
    <div>
      {!state.isEditing ? (
        <button 
          style={{ 
            cursor: 'pointer'
          }}
          onClick={() => setState({
            isEditing: true,
            date: currentDate,
            type: 'discussion',
          })}
        >
          Commencer
        </button>
      ) : (
        
        <div
          style={{
            position: 'relative',
            border: '1px solid lightgrey',
            borderRadius: 8,
            marginBottom: 10,
            padding: 10,
            paddingTop: 0,
          }}
        >
          <h4>{"Enregistrer l'activité"}</h4>
          <div>
            Date:<br/>
            <input 
              value={state.date}
              onChange={({ target }) => onSetField('date', target.value)}
            />
          </div>
          <br/>
          <div>
            Type:
            <br/>
            <select 
              value={state.type}
              onChange={e => onSetField('type', e.target.value)}
            >
              {_.map(activityOptions,({ id, text }) => (
                <option key={id} value={id}>{text}</option>
              ))}
            </select>
          </div>
          <br/>
          <button
            style={{ cursor: 'pointer' }} 
            onClick={() => setState({})}
          >
            Annuler
          </button>
          <button
            style={{ cursor: 'pointer', marginLeft: 10 }} 
            onClick={() => {
              setState({});
              addToTimeline({ 
                source: 'manual',
                date: state.date, 
                type: state.type,
                text: _.findWhere(activityOptions, { id: state.type }).text
              });
            }}
          >Enregister</button>
        </div>
      )}
    </div>
  )
}

export default ActivityEditor;