import _ from "underscore";
import { getDayAfterBusinessOffset, getDayAfterOffset, getDiffDatesInDays } from "../../common";
import { getContractPeriods, getContractSmartEndDate } from "./helpers";
import metaParameters from "../metaParameters";

// DIFF ISMA

export const getContractFollowupEvents = ({ 
  candidateState,
  contract,  
  startEvents, 
  endEvents,
  currentDate
}) => {
  let lastTouchpointDate = candidateState.lastTouchpointDate;
  const { lastActivationDate } = candidateState;

  let lastContractTouchpointDate = contract.lastTouchpointDate;
  if (lastActivationDate) {
    if (!lastContractTouchpointDate || lastActivationDate > lastContractTouchpointDate) {
      lastContractTouchpointDate = lastActivationDate;
    }
  }


  let rangeStartDate = contract.startDate;
  for (const startEvent of startEvents) {
    if (startEvent.date >= rangeStartDate) {
      rangeStartDate = startEvent.date;
    }
  }
  if (lastContractTouchpointDate >= rangeStartDate) {
    rangeStartDate = lastContractTouchpointDate;
  }
  rangeStartDate = getDayAfterBusinessOffset(rangeStartDate, 1);
  
 
  const smartEnd = getContractSmartEndDate({ contract });
  let rangeEndDate = smartEnd;
  for (const endEvent of endEvents) {
    if(endEvent.date <= rangeEndDate) {
      rangeEndDate = endEvent.date; 
    }
  }

  // we do not followup on week with FM events
  rangeEndDate = getDayAfterOffset(rangeEndDate, -(new Date(rangeEndDate).getDay())-2);

  if (getDiffDatesInDays(rangeStartDate, rangeEndDate) < 1) {
    return [];
  }

  const periods = getContractPeriods(
    contract, 
    metaParameters.contractFollowupEvents.referencePeriodSplit 
  );

  if (_.isEmpty(periods)) {
    return [];
  }

  const getLengthTypeOfPeriod = (period) => (
    getDiffDatesInDays(period.startDate, period.endDate) >= metaParameters.minNbDaysForBeingLongPeriod ? (
      'long'
    ) : 'short'
  );

  const behaviourByMode = metaParameters.contractFollowupEvents.followup.behaviourByMode;

  const followupEvents = [];
  let iPeriod = 0;
  let currentDay = rangeStartDate;
  let nbCumulatedDelayDays = 1;
  let currentMode = getLengthTypeOfPeriod(periods[0]);
  let currentBehaviour = behaviourByMode[currentMode];

  const pushEventIfEnabled = () => {
    if (
      currentDay >= currentDate && 
      nbCumulatedDelayDays >= currentBehaviour.delayInBusinessDays
    ) {
      let maxReferenceDueDate = getDayAfterBusinessOffset(
        currentDay,
        currentBehaviour.delayInBusinessDays - nbCumulatedDelayDays, // #LAURENT>OK
      );
      if (lastTouchpointDate > maxReferenceDueDate) {
        maxReferenceDueDate = lastTouchpointDate;
      }
        
      followupEvents.push({
        contractId: contract.id,
        contractName: contract.name,
        type: 'followup',
        mode: getLengthTypeOfPeriod(periods[iPeriod]),
        ...currentBehaviour.advanceCostPerNbDays && {
          advanceCostPerNbDays: currentBehaviour.advanceCostPerNbDays
        },
        ...currentBehaviour.costPerLateDay && { 
          costPerLateDay: currentBehaviour.costPerLateDay 
        },
        date: currentDay,
        maxReferenceDueDate,
        maxSnoozeDate: rangeEndDate,
      });
      nbCumulatedDelayDays = 0;
    }
  } 

  while (currentDay < rangeEndDate) {
    while (iPeriod+1 < periods.length && currentDay >= periods[iPeriod+1].startDate) {
      iPeriod++;
      currentMode = getLengthTypeOfPeriod(periods[iPeriod]);
      currentBehaviour = behaviourByMode[currentMode];
    }
    pushEventIfEnabled();
    currentDay = getDayAfterBusinessOffset(currentDay, 1);
    nbCumulatedDelayDays++;
    pushEventIfEnabled();
  }
  return followupEvents;
}