import _ from 'underscore';
import { getDayAfterOffset, getDiffDatesInDays, getFirstBusinessDayEqualOrAfter, getLastBusinessDayEqualOrBefore, getMondayOfWeek } from "../../../common";
import { CandidateState, Event, Interserie } from "../../../types";
import { getMinimalAdmissibleDate } from "./getMinimalAdmissibleDate.ts";

const getEventsFromInterserie = ({
  currentDate,
  candidateState,
  interserie,
  maxLeftEventDate,
  minRightEventDate,
} : { 
  currentDate: string;
  candidateState : CandidateState;
  interserie : Interserie;
  maxLeftEventDate: string;
  minRightEventDate: string | undefined;
}) : Event[] => {

  console.log('\n\n\n');
  console.log('compute for interserie', interserie);

  /*
  console.log('XOB', [
    getMinimalAdmissibleDate({ currentDate, candidateState }),
    interserie.startDate,
    getDayAfterOffset(maxLeftEventDate, 7),
  ]);
  */

  const getLastFollowupReferenceIndex = () : number => {
    const refDate = candidateState.lastCompletedInterserieFollowupEvent?.date;
    if (refDate) {
      const diffDays = getDiffDatesInDays(refDate, interserie.startDate);
      if (diffDays <= 5) {
        return candidateState.lastCompletedInterserieFollowupEvent?.index ?? 1;
      } 
    }
    return 0;
  }
  const startIndex = getLastFollowupReferenceIndex();  

  const startDate = getFirstBusinessDayEqualOrAfter(_.last(_.sortBy([
    getMinimalAdmissibleDate({ currentDate, candidateState }),
    interserie.startDate,
    getDayAfterOffset(maxLeftEventDate, startIndex < 2 ? 7 : 14),
  ])));

  /*
  console.log('XEB', [
    interserie.endDate,
    ...(minRightEventDate ? [
      getMondayOfWeek(minRightEventDate),
      getDayAfterOffset(maxLeftEventDate, -2),
    ] : []),
    getDayAfterOffset(currentDate, 100),
  ]);
  */


  const endDate = getLastBusinessDayEqualOrBefore(_.sortBy([
    interserie.endDate,
    ...(minRightEventDate ? [
      getMondayOfWeek(minRightEventDate),
      getDayAfterOffset(minRightEventDate, -2),
    ] : []),
    getDayAfterOffset(currentDate, 100), // to cap
  ])[0]);


  console.log({ startDate, endDate });

  if (startDate > endDate) {
    return [];
  }

  console.log({ startDate, endDate, startIndex });

  const interserieEvents : Event[] = [];
  let date = startDate;
  let index : number = 1 + (startIndex ?? 0);

  while (date <= endDate) {
    const key = `intermission-followup__${index}`;
    interserieEvents.push({
      type: 'intermission-followup',
      key,
      isMatchingRollingTask: key === candidateState.rollingTask?.key,
      date,
      maxReferenceDueDate: date,
      followupIndex: index,
    });

    date = getFirstBusinessDayEqualOrAfter(
      getDayAfterOffset(date, index < 2 ? 7 : 14)
    );
    index = index+1;
  }

  return interserieEvents;
}

export default getEventsFromInterserie;